<template>
  <div class="random_forest">
    <el-form ref="data" :model="randomFormestForm" label-position="left" label-width="160px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- objective -->
      <el-form-item label="学习目标:">
        <el-radio-group v-model="objective">
          <el-radio label="reg_squarederror">reg squarederror</el-radio>
          <el-radio label="binary_logistic">binary logistic</el-radio>
          <el-radio label="count_poisson">count poisson</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  num_trees -->
      <el-form-item label="树的个数:">
        <el-input v-model="num_trees" :placeholder="num_trees_label" id="num_trees"
                  @change="changeInputWithoutIndex('num_trees','num_trees','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('num_trees','num_trees','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('num_trees','num_trees','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!--  min_split -->
      <el-form-item label="分裂最小样本数:">
        <el-input v-model="min_split" :placeholder="min_split_label" id="min_split"
                  @change="changeInputWithoutIndex('min_split','min_split','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('min_split','min_split','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('min_split','min_split','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!--  sample_feature_num -->
      <el-form-item label="候选分裂特征数:">
        <el-input v-model="sample_feature_num" placeholder="请输入整数或选择如下选项，取值范围：[1, inf)"></el-input>
        <div>
          <el-radio-group v-model="sample_feature_num">
            <el-radio label="sqrt">sqrt</el-radio>
            <el-radio label="log2">log2</el-radio>
            <el-radio label="auto">auto</el-radio>
          </el-radio-group>
        </div>
      </el-form-item>
      <!--  max_feat_val -->
      <el-form-item label="每特征随机取值数:">
        <el-input v-model="max_feat_val" :placeholder="max_feat_val_label" id="max_feat_val"
                  @change="changeInputWithoutIndex('max_feat_val','max_feat_val','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('max_feat_val','max_feat_val','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('max_feat_val','max_feat_val','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!--  max_depth -->
      <el-form-item label="树的最大深度:">
        <el-input v-model="max_depth" :placeholder="max_depth_label" id="max_depth"
                  @change="changeInputWithoutIndex('max_depth','max_depth','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('max_depth','max_depth','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('max_depth','max_depth','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!-- dp_mech -->
      <el-form-item label="差分隐私机制:">
        <el-radio-group v-model="dp_mech">
          <el-radio label="laplace">laplace</el-radio>
          <el-radio label="piecewise">piecewise</el-radio>
          <el-radio label="duchi">duchi</el-radio>
          <el-radio label="hybrid">hybrid</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  privacy_budget -->
      <el-form-item label="差分隐私预算:">
        <el-input v-model="privacy_budget" :placeholder="privacy_budget_label" id="privacy_budget"
                  @change="changeInputWithoutIndex('privacy_budget','privacy_budget','checkGE0')"
                  @focus="focusInputWithoutIndex('privacy_budget','privacy_budget','checkGE0')"
                  @blur="blurInputWithoutIndex('privacy_budget','privacy_budget','checkGE0')"
        ></el-input>
      </el-form-item>
      <!-- encryptor -->
      <el-form-item label="同态加密算法:">
        <el-radio-group v-model="encryptor">
          <el-radio label="shac">shac</el-radio>
          <el-radio label="paillier">paillier</el-radio>
          <el-radio label="cpaillier">cpaillier</el-radio>
          <el-radio label="ou_hom">ou hom</el-radio>
          <el-radio label="ExpElGamal">ExpElGamal</el-radio>
          <el-radio label="real_vomo">real vomo</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  key_size -->
      <el-form-item label="同态加密密钥长度:">
        <el-input v-model="key_size" :placeholder="key_size_label" id="key_size"
                  @change="changeInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!--  job_num -->
      <el-form-item label="训练线程数:">
        <el-input v-model="job_num" :placeholder="job_num_label" id="job_num"
                  @change="changeInputWithoutIndex('job_num','job_num','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('job_num','job_num','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('job_num','job_num','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "RandomForest",
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','50%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_randomforest');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      randomFormestForm:{task_type:'random_forest',task_role:[],objective:[],num_trees:[],min_split:[],
        sample_feature_num:[],max_feat_val:[],max_depth:[], dp_mech:[],privacy_budget:[],key_size:[],
        encryptor:[], job_num:[]},
      //角色
      task_role:[],
      roleConfig:[],
      roleLabel:[],
      //其他参数
      objective:null,
      num_trees:null,num_trees_label:'请输入整数，取值范围：[1, inf)',
      num_trees_label_copy:'请输入整数，取值范围：[1, inf)',num_trees_time:true,
      min_split:null,min_split_label:'请输入整数，取值范围：[1, inf)',
      min_split_label_copy:'请输入整数，取值范围：[1, inf)',min_split_time:true,
      sample_feature_num:null,
      max_feat_val:null,max_feat_val_label:'请输入整数，取值范围：[1, inf)',
      max_feat_val_label_copy:'请输入整数，取值范围：[1, inf)',max_feat_val_time:true,
      max_depth:null,max_depth_label:'请输入整数，取值范围：[1, inf)',
      max_depth_label_copy:'请输入整数，取值范围：[1, inf)',max_depth_time:true,
      dp_mech:null,
      privacy_budget:null,privacy_budget_label:'请输入数值，取值范围：[0, inf)',
      privacy_budget_label_copy:'请输入数值，取值范围：[0, inf)',privacy_budget_time:true,
      key_size:null,key_size_label:'请输入整数，取值范围：[1, inf)',
      key_size_label_copy:'请输入整数，取值范围：[1, inf)',key_size_time:true,
      encryptor:null,
      job_num:null,job_num_label:'请输入整数，取值范围：[1, inf)',
      job_num_label_copy:'请输入整数，取值范围：[1, inf)',job_num_time:true,
    };
  },
  methods: {
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //初始动态化化参数
    initPara(){
      // 动态初始化角色
      let role = this.$global.formatPartyPara(this.task_role, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, '任务角色:','role');
      this.task_role = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_randomforest',this.randomFormestForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.randomFormestForm;
      }else{
        this.moduleTable.push(this.randomFormestForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.randomFormestForm = {task_type:'random_forest',task_role:[],objective:[],num_trees:[],min_split:[],
        sample_feature_num:[],max_feat_val:[],max_depth:[], dp_mech:[],privacy_budget:[],key_size:[],
        encryptor:[], job_num:[]};
      this.randomFormestForm.task_role = this.task_role;
      for(let key in this.randomFormestForm){
        if(key === 'task_type' || key === 'task_role'){
          continue;
        }
        if(this[key] !== null){
          if(this[key+'_visible']!==undefined && this[key+'_visible']===false){
            continue;
          }
          let keyInt = parseInt(this[key]);
          let keyFLoat = parseFloat(this[key]);
          let keyNum = keyInt;
          if(keyInt !== keyFLoat){
            keyNum = keyFLoat;
          }
          for(let i=0;i<this.$global.partyNumber;i++){
            if(isNaN(keyInt)){
              this.randomFormestForm[key].push(this[key]);
            }else {
              this.randomFormestForm[key].push(keyNum);
            }
          }
        }
      }
    },
    //从表单解析出来各个参数,在使用解析函数前，需要先执行initPara
    parsePara(form){
      for(let key in this.randomFormestForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //如果存在key，则对其进行赋值，如果不存在key则已经在执行解析函数前执行了其他参数的初始化，不需要对其他参数进行处理
          if(key === 'task_type'){
            continue;
          }
          if(key === 'task_role'){
            this[key] = form[key];
            continue;
          }
          if(form[key]!==null && form[key].length > 0){
            this[key] = form[key][0];
          }
        }
      }
    },
    //清空表单
    clearForm(){
      this.randomFormestForm = {task_type:'random_forest',task_role:[],objective:[],num_trees:[],min_split:[],
        sample_feature_num:[],max_feat_val:[],max_depth:[], dp_mech:[],privacy_budget:[],key_size:[],
        encryptor:[], job_num:[]};
      this.initPara();
      this.objective=null;
      this.num_trees=null;
      this.min_split=null;
      this.sample_feature_num=null;
      this.max_feat_val=null;
      this.max_depth=null;
      this.dp_mech=null;
      this.privacy_budget=null;
      this.key_size=null;
      this.encryptor=null;
      this.job_num=null;
    },
    //对参数进行检查
    checkInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(this.$global[checkType](this[para_name]) || this[para_name]===''){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
        return true;
      }else{
        obj.style.borderColor = '#e30d28';
        this[para_name] = null;
        this[para_name+'_label'] = "参数错误! " + this[para_name+'_label_copy'];
        this.$forceUpdate();
        return false;
      }
    },
    //输入框发生改变的事件
    changeInputWithoutIndex(name, para_name,checkType){
      if(this[para_name+'_time']){
        this[para_name+'_time'] = false;
      }
      this.checkInputWithoutIndex(name, para_name, checkType);
    },
    //输入框被focus触发边框颜色变化
    focusInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        obj.style.borderColor = '#409EFF';
      }
    },
    //输入框被blur触发边框颜色变化
    blurInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
      }
    }
  }
}
</script>

