<template>
  <div class="bucketizer">
    <el-form ref="data" :model="bucketizerForm" label-position="left" label-width="150px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- bucketizer_type -->
      <el-form-item v-for="(item,index) in bucketizer_type_config" :key="item.name" :label="bucketizer_type_label[index]">
        <el-radio-group v-model="bucketizer_type[index]">
          <el-radio label="quantile">quantile</el-radio>
          <el-radio label="bucket">bucket</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  num_bucket -->
      <el-form-item label="桶的数量:">
        <el-input v-for="(item,index) in num_bucket_config" :key="item.name" :id="item.name"
                  @change="changeInput(item.name,index,'num_bucket','checkPositiveInteger')"
                  @focus="focusInput(item.name,index,'num_bucket','checkPositiveInteger')"
                  @blur="blurInput(item.name,index,'num_bucket','checkPositiveInteger')"
                  v-model="num_bucket[index]" :placeholder="num_bucket_label[index]">
        </el-input>
      </el-form-item>
      <!--  target_columns -->
      <el-form-item label="目标列:">
        <el-input v-for="(item,index) in target_columns_config" :key="item.name" v-model="target_columns[index]"
                  :placeholder="target_columns_PlaceHolder[index]" @change="targetColumnsChange">
          <el-switch slot="append" v-model="target_columns_all[index]" @change="targetColumnsAllChange"></el-switch>
        </el-input>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Bucketizer",
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','40%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_bucketizer');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      bucketizer_type:[],
      bucketizer_type_config:[],
      bucketizer_type_label:[],
      num_bucket:[],
      num_bucket_config:[],
      num_bucket_label:[],
      num_bucket_label_copy:[],
      target_columns:[],
      target_columns_config:[],
      target_columns_all:[],
      target_columns_PlaceHolder:[],
      bucketizerForm:{task_type:'bucketizer',task_role:[],bucketizer_type:[],num_bucket:[],target_columns:[]},
      role_data:[{key:1,label:'guest'},
        {key:2,label:'host'}],
      role_value:['guest','host'],
      task_role:[],
      roleConfig:[],
      roleLabel:[]
    };
  },
  methods: {
    //目标列改变
    targetColumnsAllChange(){
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns_all[i]){
          this.target_columns[i] = null;
          this.target_columns_PlaceHolder[i] = '已选择全部列';
        }else {
          this.target_columns_PlaceHolder[i] = '第' + (i+1) + '个参与方,列之间用逗号分隔,如1,5; 所有列则点右侧开关';
        }
      }
    },
    targetColumnsChange(){
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns[i] !== null){
          this.target_columns_all[i] = false;
        }
      }
      this.$forceUpdate();
    },
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //初始动态化化参数
    initPara(){
      // 动态初始化角色
      let role = this.$global.formatPartyPara(this.task_role, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, '任务角色:','role')
      this.task_role = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
      // 动态初始化bucketizer_type
      let type = this.$global.formatPartyPara(this.bucketizer_type, this.bucketizer_type_config,
          this.bucketizer_type_label, this.$global.partyNumber, '分桶类型:', 'type')
      this.bucketizer_type = type[0];
      this.bucketizer_type_config = type[1];
      this.bucketizer_type_label = type[2];
      // 动态初始化num_bucket
      let num = this.$global.formatPartyParaControlShow(this.num_bucket, this.num_bucket_config,
          this.num_bucket_label, this.$global.partyNumber, '桶的数量，请输入正整数', 'num')
      this.num_bucket = num[0];
      this.num_bucket_config = num[1];
      this.num_bucket_label = num[2];
      this.num_bucket_label_copy = JSON.parse(JSON.stringify(this.num_bucket_label));
      // 动态初始化target_columns
      let columns = this.$global.formatPartyPara(this.target_columns, this.target_columns_config,
          this.target_columns_PlaceHolder, this.$global.partyNumber, ',列之间用逗号分隔,如1,5; 所有列则点右侧开关','col')
      this.target_columns = columns[0];
      this.target_columns_config = columns[1];
      this.target_columns_PlaceHolder = columns[2];
      this.target_columns_all = this.$global.formatList(this.target_columns_all, this.$global.partyNumber);
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_bucketizer',this.bucketizerForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.bucketizerForm;
      }else{
        this.moduleTable.push(this.bucketizerForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.bucketizerForm = {task_type:'bucketizer',task_role:[],bucketizer_type:[],num_bucket:[],target_columns:[]};
      this.bucketizerForm.task_role = this.task_role;
      this.bucketizerForm.bucketizer_type = this.bucketizer_type;
      this.bucketizerForm.num_bucket = this.$global.parseStrArray2IntArray(this.num_bucket);
      for(let i=0;i<this.$global.partyNumber;i++){
        if(this.target_columns_all[i]){
          this.bucketizerForm.target_columns.push('all_columns');
        }else {
          this.bucketizerForm.target_columns.push(this.$global.parseInput2IntArray(this.target_columns[i]));
        }
      }
    },
    //从表单解析出来各个参数,在使用解析函数前，需要先执行initPara
    parsePara(form){
      for(let key in this.bucketizerForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //如果存在key，则对其进行赋值，如果不存在key则已经在执行解析函数前执行了其他参数的初始化，不需要对其他参数进行处理
          if(key === 'task_type'){
            continue;
          }
          if(key === 'target_columns'){
            for(let i=0;i<form[key].length;i++){
              if(form[key][i] === 'all_columns'){
                this.target_columns_all[i] = true;
                this.target_columns_PlaceHolder[i] = '已选择全部列';
              }else {
                this.target_columns[i] = this.$global.array2str(form[key][i]);
              }
            }
          }else if(form[key]!==null && this[key].length === form[key].length){
            this[key] = form[key];
          }
        }
      }
    },
    //清空表单
    clearForm(){
      this.bucketizerForm = {task_type:'bucketizer',task_role:[],bucketizer_type:[],num_bucket:[],target_columns:[]};
      this.initPara();
    },
    //对参数进行检查
    checkInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(this.$global[checkType](this[para_name][index]) || this[para_name][index]===''){
        this[para_name+'_label'][index] = this[para_name+'_label_copy'][index];
        obj.style.borderColor = '#DCDFE6';
        return true;
      }else{
        obj.style.borderColor = '#e30d28';
        this[para_name][index] = null;
        this[para_name+'_label'][index] = "参数错误! " + this[para_name+'_label_copy'][index];
        this.$forceUpdate();
        return false;
      }
    },
    //输入框发生改变的事件
    changeInput(name, index, para_name,checkType){
      if(this[para_name+'_config'][index].isFirstTime){
        this[para_name+'_config'][index].isFirstTime = false;
      }
      this.checkInput(name, index, para_name,checkType);
    },
    //输入框被focus触发边框颜色变化
    focusInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_config'][index].isFirstTime && this.checkInput(name,index,para_name,checkType)){
        obj.style.borderColor = '#409EFF';
      }
    },
    //输入框被blur触发边框颜色变化
    blurInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_config'][index].isFirstTime && this.checkInput(name,index,para_name,checkType)){
        this[para_name+'_label'][index] = this[para_name+'_label_copy'][index];
        obj.style.borderColor = '#DCDFE6';
      }
    }
  }
}
</script>

