<template>
  <div class="glm">
    <el-form ref="data" :model="glmForm" label-position="left" label-width="160px">
      <!-- task type -->
      <el-form-item label="任务类型:">
        <el-radio-group v-model="task_type">
          <el-radio label="linear_regression">linear regression</el-radio>
          <el-radio label="logistic_regression">logistic regression</el-radio>
          <el-radio label="poisson_regression">poisson regression</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
          <el-radio label="server">server</el-radio>
          <el-radio label="client">client</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- penalty -->
      <el-form-item label="惩罚函数:">
        <el-radio-group v-model="penalty">
          <el-radio label="l1">l1</el-radio>
          <el-radio label="l2">l2</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  tol -->
      <el-form-item label="容忍度:">
        <el-input v-model="tol" :placeholder="tol_label" id="tol"
                  @change="changeInputWithoutIndex('tol','tol','checkGE0')"
                  @focus="focusInputWithoutIndex('tol','tol','checkGE0')"
                  @blur="blurInputWithoutIndex('tol','tol','checkGE0')"
        ></el-input>
      </el-form-item>
      <!--  C -->
      <el-form-item label="正则化强度:">
        <el-input v-model="C" :placeholder="C_label" id="C"
                  @change="changeInputWithoutIndex('C','C','checkGL0')"
                  @focus="focusInputWithoutIndex('C','C','checkGL0')"
                  @blur="blurInputWithoutIndex('C','C','checkGL0')"></el-input>
      </el-form-item>
      <!-- fit_intercept -->
      <el-form-item label="拟合截距:">
        <el-switch v-model="fit_intercept" active-color="#13ce66"></el-switch>
      </el-form-item>
      <!--  intercept_scaling -->
      <el-form-item label="截距缩放:">
        <el-input v-model="intercept_scaling" :placeholder="intercept_scaling_label" id="isl"
                  @change="changeInputWithoutIndex('isl','intercept_scaling','checkGE0')"
                  @focus="focusInputWithoutIndex('isl','intercept_scaling','checkGE0')"
                  @blur="blurInputWithoutIndex('isl','intercept_scaling','checkGE0')"
        ></el-input>
      </el-form-item>
      <!-- solver -->
      <el-form-item label="求解器:">
        <el-radio-group v-model="solver" @change="solverChange">
          <el-radio label="gradient_descent">gradient descent</el-radio>
          <el-radio label="AdaGram">AdaGram</el-radio>
          <el-radio label="AdaDelta">AdaDelta</el-radio>
          <el-radio label="RMSprop">RMSprop</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  max_iter -->
      <el-form-item label="最大迭代轮数:">
        <el-input v-model="max_iter" :placeholder="max_iter_label" id="max_iter"
                  @change="changeInputWithoutIndex('max_iter','max_iter','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('max_iter','max_iter','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('max_iter','max_iter','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!--  learning_rate -->
      <el-form-item label="学习率:">
        <el-input v-model="learning_rate" :placeholder="learning_rate_label" id="learning_rate"
                  @change="changeInputWithoutIndex('learning_rate','learning_rate','checkGL0')"
                  @focus="focusInputWithoutIndex('learning_rate','learning_rate','checkGL0')"
                  @blur="blurInputWithoutIndex('learning_rate','learning_rate','checkGL0')"
        ></el-input>
      </el-form-item>
      <!-- homomorphism -->
      <el-form-item label="同态加密算法:">
        <el-radio-group v-model="homomorphism">
          <el-radio label="cpaillier">cpaillier</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  key_size -->
      <el-form-item label="同态加密密钥长度:">
        <el-input v-model="key_size" :placeholder="key_size_label" id="key_size"
                  @change="changeInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!--  gamma -->
      <el-form-item label="梯度累积率:" v-if="gamma_visible">
        <el-input v-model="gamma" :placeholder="gamma_label" id="gamma"
                  @change="changeInputWithoutIndex('gamma','gamma','checkGL0')"
                  @focus="focusInputWithoutIndex('gamma','gamma','checkGL0')"
                  @blur="blurInputWithoutIndex('gamma','gamma','checkGL0')"
        ></el-input>
      </el-form-item>
      <!--  epsilon -->
      <el-form-item label="平滑系数:" v-if="epsilon_visible">
        <el-input v-model="epsilon" :placeholder="epsilon_label" id="epsilon"
                  @change="changeInputWithoutIndex('epsilon','epsilon','checkGL0')"
                  @focus="focusInputWithoutIndex('epsilon','epsilon','checkGL0')"
                  @blur="blurInputWithoutIndex('epsilon','epsilon','checkGL0')"
        ></el-input>
      </el-form-item>
      <!-- batch_type -->
      <el-form-item label="batch类型:">
        <el-radio-group v-model="batch_type" @change="batchTypeChange">
          <el-radio label="batch">batch</el-radio>
          <el-radio label="mini-batch">mini-batch</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  batch_fraction -->
      <el-form-item label="batch大小比率:" v-if="batch_fraction_visible">
        <el-input v-model="batch_fraction" :placeholder="batch_fraction_label" id="batch_fraction"
                  @change="changeInputWithoutIndex('batch_fraction','batch_fraction','checkL02E1')"
                  @focus="focusInputWithoutIndex('batch_fraction','batch_fraction','checkL02E1')"
                  @blur="blurInputWithoutIndex('batch_fraction','batch_fraction','checkL02E1')"
        ></el-input>
      </el-form-item>
      <!-- balanced_class_weight -->
      <el-form-item label="平衡类权重:">
        <el-switch v-model="balanced_class_weight" active-color="#13ce66"></el-switch>
      </el-form-item>
      <!--  train_validate_freq -->
      <el-form-item label="训练验证频率:">
        <el-input v-model="train_validate_freq" :placeholder="train_validate_freq_label" id="train_validate_freq"
                  @change="changeInputWithoutIndex('train_validate_freq','train_validate_freq','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('train_validate_freq','train_validate_freq','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('train_validate_freq','train_validate_freq','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Glm",
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','50%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_glm');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      glmForm:{task_type:'',task_role:[],penalty:[],tol:[],C:[],fit_intercept:[],intercept_scaling:[],
        solver:[],max_iter:[],learning_rate:[],homomorphism:[],key_size:[], gamma:[],epsilon:[], batch_fraction:[],
        batch_type:[],balanced_class_weight:[],train_validate_freq:[]},
      //角色
      task_role:[],
      roleConfig:[],
      roleLabel:[],
      //其他参数
      task_type:null,
      penalty:null,
      //tol
      tol:null, tol_label:'请输入数值，取值范围：[0, inf)',tol_label_copy:'请输入数值，取值范围：[0, inf)',tol_time:true,
      //C
      C:null, C_label:'请输入数值，取值范围：(0, inf)', C_label_copy:'请输入数值，取值范围：(0, inf)', C_time:true,
      fit_intercept:null,
      //intercept_scaling
      intercept_scaling:null,intercept_scaling_label:'请输入数值，取值范围：[0, inf)',
      intercept_scaling_label_copy:'请输入数值，取值范围：[0, inf)', intercept_scaling_time:true,
      solver:null,
      //max_iter
      max_iter:null,max_iter_label:'请输入整数，取值范围：[1, inf)',
      max_iter_label_copy:'请输入整数，取值范围：[1, inf)',max_iter_time:true,
      //learning_rate
      learning_rate:null,learning_rate_label:'请输入整数，取值范围：(0, inf)',
      learning_rate_label_copy:'请输入整数，取值范围：(0, inf)',learning_rate_time:true,
      homomorphism:null,
      //key_size
      key_size:null,key_size_label:'请输入整数，取值范围：[1, inf)',
      key_size_label_copy:'请输入整数，取值范围：[1, inf)',key_size_time:true,
      //gamma
      gamma:null,gamma_label:'请输入数值，取值范围：(0, inf)',gamma_label_copy:'请输入数值，取值范围：(0, inf)',gamma_time:true,
      //epsilon
      epsilon:null,epsilon_label:'请输入数值，取值范围：(0, inf)',
      epsilon_label_copy:'请输入数值，取值范围：(0, inf)',epsilon_time:true,
      //batch_fraction
      batch_fraction:null,batch_fraction_label:'请输入数值，取值范围：(0, 1]',
      batch_fraction_label_copy:'请输入数值，取值范围：(0, 1]',batch_fraction_time:true,
      batch_type:null,
      balanced_class_weight:null,
      //train_validate_freq
      train_validate_freq:null,train_validate_freq_label:'请输入整数，取值范围：[1, inf)',
      train_validate_freq_label_copy:'请输入整数，取值范围：[1, inf)',train_validate_freq_time:true,
      //可视化
      batch_fraction_visible:false,
      gamma_visible:false,
      epsilon_visible:false
    };
  },
  methods: {
    batchTypeChange(){
      this.batch_fraction_visible = this.batch_type === 'mini-batch';
    },
    solverChange(){
      this.gamma_visible = false;
      this.epsilon_visible = false;
      if(this.solver === 'AdaGram' || this.solver === 'AdaDelta' || this.solver === 'RMSprop'){
        this.gamma_visible = true;
        this.epsilon_visible = true;
      }
    },
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //初始动态化化参数
    initPara(){
      // 动态初始化角色
      let role = this.$global.formatPartyPara(this.task_role, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, '任务角色:','role');
      this.task_role = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_glm',this.glmForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.glmForm;
      }else{
        this.moduleTable.push(this.glmForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.glmForm = {task_type:'',task_role:[],penalty:[],tol:[],C:[],fit_intercept:[],intercept_scaling:[],
        solver:[],max_iter:[],learning_rate:[],homomorphism:[],key_size:[], gamma:[],epsilon:[], batch_fraction:[],
        batch_type:[],balanced_class_weight:[],train_validate_freq:[]};
      this.glmForm.task_role = this.task_role;
      this.glmForm.task_type = this.task_type;
      for(let key in this.glmForm){
        if(key === 'task_type' || key === 'task_role'){
          continue;
        }
        if(this[key] !== null){
          if(this[key+'_visible']!==undefined && this[key+'_visible']===false){
            continue;
          }
          let keyInt = parseInt(this[key]);
          let keyFLoat = parseFloat(this[key]);
          let keyNum = keyInt;
          if(keyInt !== keyFLoat){
            keyNum = keyFLoat;
          }
          for(let i=0;i<this.$global.partyNumber;i++){
            if(isNaN(keyInt)){
              this.glmForm[key].push(this[key]);
            }else {
              this.glmForm[key].push(keyNum);
            }
          }
        }
      }
    },
    //从表单解析出来各个参数,在使用解析函数前，需要先执行initPara
    parsePara(form){
      this.task_type = form.task_type;
      for(let key in this.glmForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //如果存在key，则对其进行赋值，如果不存在key则已经在执行解析函数前执行了其他参数的初始化，不需要对其他参数进行处理
          if(key === 'task_type'){
            continue;
          }
          if(key === 'task_role'){
            this[key] = form[key];
            continue;
          }
          if(form[key]!==null && form[key].length > 0){
            this[key] = form[key][0];
          }
        }
      }
      this.batchTypeChange();
      this.solverChange();
    },
    //清空表单
    clearForm(){
      this.glmForm = {task_type:'',task_role:[],penalty:[],tol:[],C:[],fit_intercept:[],intercept_scaling:[],
        solver:[],max_iter:[],learning_rate:[],homomorphism:[],key_size:[], gamma:[],epsilon:[], batch_fraction:[],
        batch_type:[],balanced_class_weight:[],train_validate_freq:[]};
      this.initPara();
      this.task_type=null;
      this.penalty=null;
      this.tol=null;
      this.C=null;
      this.fit_intercept=null;
      this.intercept_scaling=null;
      this.solver=null;
      this.max_iter=null;
      this.learning_rate=null;
      this.homomorphism=null;
      this.key_size=null;
      this.gamma=null;
      this.epsilon=null;
      this.batch_fraction=null;
      this.batch_type=null;
      this.balanced_class_weight=null;
      this.train_validate_freq=null;
      //可视化
      this.batch_fraction_visible=false;
      this.gamma_visible=false;
      this.epsilon_visible=false;
    },
    //对参数进行检查
    checkInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(this.$global[checkType](this[para_name]) || this[para_name]===''){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
        return true;
      }else{
        obj.style.borderColor = '#e30d28';
        this[para_name] = null;
        this[para_name+'_label'] = "参数错误! " + this[para_name+'_label_copy'];
        this.$forceUpdate();
        return false;
      }
    },
    //输入框发生改变的事件
    changeInputWithoutIndex(name, para_name,checkType){
      if(this[para_name+'_time']){
        this[para_name+'_time'] = false;
      }
      this.checkInputWithoutIndex(name, para_name, checkType);
    },
    //输入框被focus触发边框颜色变化
    focusInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        obj.style.borderColor = '#409EFF';
      }
    },
    //输入框被blur触发边框颜色变化
    blurInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
      }
    }
  }
}
</script>

<style scoped>

</style>