<template>
  <div class="data_saver">
    <el-form ref="data" :model="dataSaverForm" label-position="left" label-width="130px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
          <el-radio label="sole">sole</el-radio>
          <el-radio label="slack">slack</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- output data source -->
      <el-form-item label="输出数据类型:">
        <el-radio v-model="output_data_source" label="csv">csv</el-radio>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "DataSaver",
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','35%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_datasaver');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      output_data_source: null,
      dataSaverForm:{task_type: 'data_saver',task_role: null,output_data_source: null},
      task_role:[],
      roleConfig:[],
      roleLabel:[]
    };
  },
  methods: {
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //初始动态化化参数
    initPara(){
      //动态增加角色
      let role = this.$global.formatPartyPara(this.task_role, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, '角色:', 'role')
      this.task_role = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_datasaver',this.dataSaverForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.dataSaverForm;
      }else{
        this.moduleTable.push(this.dataSaverForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.dataSaverForm.task_role = this.task_role;
      this.dataSaverForm.output_data_source = [];
      for(let i=0;i<this.$global.partyNumber;i++){
        this.dataSaverForm.output_data_source.push(this.output_data_source);
      }
    },
    //从表单解析出来各个参数,在使用解析函数前，需要先执行initPara
    parsePara(form){
      for(let key in this.dataSaverForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //如果存在key，则对其进行赋值，如果不存在key则已经在执行解析函数前执行了其他参数的初始化，不需要对其他参数进行处理
          if(key === 'task_type'){
            continue;
          }
          if(key === 'output_data_source'){
            if(form[key]!== null){
              this[key] = form[key][0];
            }
          }else if(form[key]!==null && this[key].length === form[key].length){
            this[key] = form[key];
          }
        }
      }
    },
    //清空表单
    clearForm(){
      this.dataSaverForm = {task_type: 'data_saver',task_role: null,output_data_source: null};
      this.initPara();
      this.output_data_source = 'csv';
    },
  }
}
</script>

