<template>
  <div class="imputation">
    <el-form ref="data" :model="imputationForm" label-position="left" label-width="150px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- imputation_type插值类型 -->
      <el-form-item v-for="(item,index) in imputation_type_config" :key="item.name" :label="imputation_type_label[index]">
        <el-radio-group v-model="imputation_type[index]" @change="imputationTypeChange">
          <el-radio label="mean">mean</el-radio>
          <el-radio label="median">median</el-radio>
          <el-radio label="mode">mode</el-radio>
          <el-radio label="linear_regression">linear regression</el-radio>
          <el-radio label="glm">glm</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  max_iter 最大迭代次数 -->
      <el-form-item label="最大迭代次数:" v-if="max_iter_visible">
        <el-input v-for="(item,index) in max_iter_config" :key="item.name" v-show="item.isShow" :id="item.name"
                  @change="changeInput(item.name,index,'max_iter','checkPositiveInteger')"
                  @focus="focusInput(item.name,index,'max_iter','checkPositiveInteger')"
                  @blur="blurInput(item.name,index,'max_iter','checkPositiveInteger')"
                  v-model="max_iter[index]" :placeholder="max_iter_label[index]">
        </el-input>
      </el-form-item>
      <!--  reg_param 正则化参数 -->
      <el-form-item label="正则化参数:" v-if="reg_param_visible">
        <el-input v-for="(item,index) in reg_param_config" :key="item.name" v-show="item.isShow" :id="item.name"
                  @change="changeInput(item.name,index,'reg_param','checkE02E1')"
                  @focus="focusInput(item.name,index,'reg_param','checkE02E1')"
                  @blur="blurInput(item.name,index,'reg_param','checkE02E1')"
                  v-model="reg_param[index]" :placeholder="reg_param_label[index]">
        </el-input>
      </el-form-item>
      <!--  elastic_net_param 弹性网参数 -->
      <el-form-item label="弹性网参数:" v-if="elastic_net_param_visible">
        <el-input v-for="(item,index) in elastic_net_param_config" :key="item.name" v-show="item.isShow" :id="item.name"
                  @change="changeInput(item.name,index,'elastic_net_param','checkGE0')"
                  @focus="focusInput(item.name,index,'elastic_net_param','checkGE0')"
                  @blur="blurInput(item.name,index,'elastic_net_param','checkGE0')"
                  v-model="elastic_net_param[index]" :placeholder="elastic_net_param_label[index]">
        </el-input>
      </el-form-item>
      <div v-if="family_visible">
        <el-form-item  v-for="(item,index) in family_config" v-show="item.isShow"
                       :key="item.name" :label="family_label[index]">
          <el-radio-group v-model="family[index]">
            <el-radio label="gaussian">gaussian</el-radio>
            <el-radio label="binomial">binomial</el-radio>
            <el-radio label="poisson">poisson</el-radio>
            <el-radio label="gamma">gamma</el-radio>
            <el-radio label="tweedie">tweedie</el-radio>
          </el-radio-group>
        </el-form-item>
      </div>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Imputation",
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','50%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_imputation');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      //分布
      family:[],
      family_config:[],
      family_label:[],
      //正则化参数
      reg_param:[],
      reg_param_config:[],
      reg_param_label:[],
      reg_param_config_copy:[],
      reg_param_label_copy:[],
      //elastic_net_param
      elastic_net_param:[],
      elastic_net_param_config:[],
      elastic_net_param_label:[],
      elastic_net_param_config_copy:[],
      elastic_net_param_label_copy:[],
      //最大迭代次数
      max_iter:[],
      max_iter_config:[],
      max_iter_label:[],
      max_iter_label_copy:[],
      max_iter_config_copy:[],
      //填充类型
      imputation_type:[],
      imputation_type_config:[],
      imputation_type_label:[],
      imputationForm:{task_type:'imputation',task_role:[],imputation_type:[],max_iter:[],reg_param:[],
        elastic_net_param:[],family:[]},
      //角色
      role_data:[{key:1,label:'guest'},
        {key:2,label:'host'}],
      role_value:['guest','host'],
      task_role:[],
      roleConfig:[],
      roleLabel:[],
      // 参数是否可见
      max_iter_visible: false,
      reg_param_visible: false,
      elastic_net_param_visible: false,
      family_visible: false
    };
  },
  methods: {
    // 监听填充类型改变来控制其他选项的展示效果
    imputationTypeChange(){
      // init config & visible
      this.max_iter_config = [];
      this.max_iter_visible = false;
      this.reg_param_config = [];
      this.reg_param_visible = false;
      this.elastic_net_param_config = [];
      this.elastic_net_param_visible = false;
      this.family_config = [];
      this.family_visible = false;
      for(let i=0; i<this.imputation_type.length; i++){
        let iter_config = {'name': 'iter' + (i+1), 'isShow': false};
        let reg_config = {'name': 'reg' + (i+1), 'isShow': false};
        let net_config = {'name': 'net' + (i+1), 'isShow': false};
        let fa_config = {'name': 'fa' + (i+1), 'isShow': false};
        if(this.imputation_type[i] === 'glm'){
          iter_config.isShow = true;
          reg_config.isShow = true;
          net_config.isShow = true;
          fa_config.isShow = true;
          this.max_iter_visible = true;
          this.reg_param_visible = true;
          this.elastic_net_param_visible = true;
          this.family_visible = true;
        }
        if(this.imputation_type[i] === 'linear_regression'){
          iter_config.isShow = true;
          reg_config.isShow = true;
          net_config.isShow = true;
          this.max_iter_visible = true;
          this.reg_param_visible = true;
          this.elastic_net_param_visible = true;
        }
        this.max_iter_config.push(iter_config);
        this.reg_param_config.push(reg_config);
        this.elastic_net_param_config.push(net_config);
        this.family_config.push(fa_config);
      }
    },
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //初始动态化化参数
    initPara(){
      // 动态初始化角色
      let role = this.$global.formatPartyPara(this.task_role, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, '任务角色:','role')
      this.task_role = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
      // 动态初始化imputation_type
      let imp = this.$global.formatPartyPara(this.imputation_type, this.imputation_type_config,
          this.imputation_type_label, this.$global.partyNumber, '填充类型:', 'imp')
      this.imputation_type = imp[0];
      this.imputation_type_config = imp[1];
      this.imputation_type_label = imp[2];
      // 动态初始化max_iter
      let iter = this.$global.formatPartyParaControlShow(this.max_iter, this.max_iter_config,
          this.max_iter_label, this.$global.partyNumber, '最大迭代次数，请输入正整数', 'iter')
      this.max_iter = iter[0];
      this.max_iter_config = iter[1];
      this.max_iter_label = iter[2];
      this.max_iter_label_copy = JSON.parse(JSON.stringify(this.max_iter_label));
      this.max_iter_config_copy = this.$global.copyJson(this.max_iter_config);
      // 动态初始化reg_param
      let reg = this.$global.formatPartyParaControlShow(this.reg_param, this.reg_param_config,
          this.reg_param_label, this.$global.partyNumber, '正则化参数，参数范围: [0, 1]', 'reg')
      this.reg_param = reg[0];
      this.reg_param_config = reg[1];
      this.reg_param_label = reg[2];
      this.reg_param_config_copy = this.$global.copyJson(this.reg_param_config);
      this.reg_param_label_copy = this.$global.copyJson(this.reg_param_label);
      // 动态初始化elastic_net_param
      let net = this.$global.formatPartyParaControlShow(this.elastic_net_param, this.elastic_net_param_config,
          this.elastic_net_param_label, this.$global.partyNumber, '动态网参数，参数范围: [0, inf)', 'net')
      this.elastic_net_param = net[0];
      this.elastic_net_param_config = net[1];
      this.elastic_net_param_label = net[2];
      this.elastic_net_param_config_copy = this.$global.copyJson(this.elastic_net_param_config);
      this.elastic_net_param_label_copy = this.$global.copyJson(this.elastic_net_param_label);
      // 动态初始化family
      let fa = this.$global.formatPartyParaControlShow(this.family, this.family_config,
          this.family_label, this.$global.partyNumber, '分布:', 'fa')
      this.family = fa[0];
      this.family_config = fa[1];
      this.family_label = fa[2];
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_imputation',this.imputationForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.imputationForm;
      }else{
        this.moduleTable.push(this.imputationForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.imputationForm = {task_type:'imputation',task_role:[],imputation_type:[],max_iter:[],
        reg_param:[],elastic_net_param:[],family:[]};
      this.imputationForm.task_role = this.task_role;
      this.imputationForm.imputation_type = this.imputation_type;
      if(this.max_iter_visible){this.imputationForm.max_iter =
          this.$global.handleIsShowPara(this.max_iter,this.max_iter_config,'int');}
      if(this.reg_param_visible){this.imputationForm.reg_param =
          this.$global.handleIsShowPara(this.reg_param,this.reg_param_config,'float');}
      if(this.elastic_net_param_visible){this.imputationForm.elastic_net_param =
          this.$global.handleIsShowPara(this.elastic_net_param,this.elastic_net_param_config,'float');}
      if(this.family_visible){this.imputationForm.family =
          this.$global.handleIsShowPara(this.family,this.family_config,'');}
    },
    //从表单解析出来各个参数,在使用解析函数前，需要先执行initPara
    parsePara(form){
      for(let key in this.imputationForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //如果存在key，则对其进行赋值，如果不存在key则已经在执行解析函数前执行了其他参数的初始化，不需要对其他参数进行处理
          if(key === 'task_type'){
            continue;
          }
          if(key === 'imputation_type' || key==='task_role'){
            this[key] = form[key];
            continue;
          }
          if(form[key]!==null && this[key].length === form[key].length){
            for(let fi = 0;fi<this[key].length;fi++){
              if(form[key][fi] === null || isNaN(form[key][fi])){
                this[key][fi] = null;
              }
              this[key][fi] = form[key][fi];
            }
          }
        }
      }
      this.imputationTypeChange();
    },
    //清空表单
    clearForm(){
      this.imputationForm = {task_type:'imputation',task_role:[],imputation_type:[],max_iter:[],
        reg_param:[],elastic_net_param:[],family:[]};
      this.initPara();
      this.max_iter_visible=false;
      this.reg_param_visible=false;
      this.elastic_net_param_visible=false;
      this.family_visible=false;
    },
    //对参数进行检查
    checkInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(this.$global[checkType](this[para_name][index]) || this[para_name][index]===''){
        this[para_name+'_label'][index] = this[para_name+'_label_copy'][index];
        obj.style.borderColor = '#DCDFE6';
        return true;
      }else{
        obj.style.borderColor = '#e30d28';
        this[para_name][index] = null;
        this[para_name+'_label'][index] = "参数错误! " + this[para_name+'_label_copy'][index];
        this.$forceUpdate();
        return false;
      }
    },
    //输入框发生改变的事件
    changeInput(name, index, para_name,checkType){
      if(this[para_name+'_config_copy'][index].isFirstTime){
        this[para_name+'_config_copy'][index].isFirstTime = false;
      }
      this.checkInput(name, index, para_name,checkType);
    },
    //输入框被focus触发边框颜色变化
    focusInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_config_copy'][index].isFirstTime && this.checkInput(name,index,para_name,checkType)){
        obj.style.borderColor = '#409EFF';
      }
    },
    //输入框被blur触发边框颜色变化
    blurInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_config_copy'][index].isFirstTime && this.checkInput(name,index,para_name,checkType)){
        this[para_name+'_label'][index] = this[para_name+'_label_copy'][index];
        obj.style.borderColor = '#DCDFE6';
      }
    }
  }
}
</script>

