<template>
  <div class="model_loader">
    <el-form ref="data" :model="modelLoaderForm" label-position="left" label-width="150px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="roles[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  model_id -->
      <el-form-item label="模型id:">
        <el-input v-model="model_id" placeholder="请输入模型id"></el-input>
      </el-form-item>
      <!--  action -->
      <el-form-item label="是否加载模型:">
        <el-switch v-model="action" active-color="#13ce66" @change="invaction = !action"></el-switch>
      </el-form-item>
      <el-form-item label="是否卸载模型:">
        <el-switch v-model="invaction" active-color="#13ce66" @change="action = !invaction"></el-switch>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "ModelLoader",
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','35%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_modelloader');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      modelLoaderForm:{task_type:'model_loader',task_role:[],model_id:[],action:''},
      model_id:null,
      action:true,
      invaction:null,
      //角色
      role_data:[{key:1,label:'guest'},
        {key:2,label:'host'}],
      role_value:['guest','host'],
      roles:[],
      roleConfig:[],
      roleLabel:[],
    };
  },
  methods: {
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //初始动态化化参数
    initPara(){
      // 动态初始化角色
      let role = this.$global.formatPartyPara(this.roles, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, '任务角色:','role');
      this.roles = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_modelloader',this.modelLoaderForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.modelLoaderForm;
      }else{
        this.moduleTable.push(this.modelLoaderForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.modelLoaderForm = {task_type:'model_loader',task_role:[],model_id:[],action:''};
      this.modelLoaderForm.task_role = this.roles;
      this.modelLoaderForm.action = this.action;
      if(this.action){
        this.modelLoaderForm.action = 'load';
      }else{
        this.modelLoaderForm.action = 'unload';
      }
      for(let i=0;i<this.$global.partyNumber;i++){
        this.modelLoaderForm.model_id.push(this.model_id);
      }
    },
    //从表单解析出来各个参数
    parsePara(form){
      this.roles = form.task_role;
      if(form.action === 'load'){
        this.action = true;
        this.invaction = false;
      }else{
        this.action = false;
        this.invaction = true;
      }
      this.model_id = form.model_id[0];
      this.invaction = !this.action;
    },
    //清空表单
    clearForm(){
      this.modelLoaderForm = {task_type:'model_loader',task_role:[],model_id:[],action:''};
      this.initPara();
      this.action = true;
      this.model_id = null;
      this.invaction = !this.action;
    },
  }
}
</script>

