<template>
  <div class="dpgbdt">
    <el-form ref="data" :model="dpgbdtForm" label-position="left" label-width="160px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- objective -->
      <el-form-item label="学习目标:">
        <el-radio-group v-model="objective">
          <el-radio label="reg_squarederror">reg squarederror</el-radio>
          <el-radio label="binary_logistic">binary logistic</el-radio>
          <el-radio label="count_poisson">count poisson</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  num_round -->
      <el-form-item label="轮数:">
        <el-input v-model="num_round" :placeholder="num_round_label" id="nr"
                  @change="changeInputWithoutIndex('nr','num_round','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('nr','num_round','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('nr','num_round','checkPositiveInteger')"
        > </el-input>
      </el-form-item>
      <!--  eta -->
      <el-form-item label="学习率:">
        <el-input v-model="eta" :placeholder="eta_label" id="eta"
                  @change="changeInputWithoutIndex('eta','eta','checkGL0')"
                  @focus="focusInputWithoutIndex('eta','eta','checkGL0')"
                  @blur="blurInputWithoutIndex('eta','eta','checkGL0')"
        > </el-input>
      </el-form-item>
      <!--  gamma -->
      <el-form-item label="最小分裂增益:">
        <el-input v-model="gamma" :placeholder="gamma_label" id="gamma"
                  @change="changeInputWithoutIndex('gamma','gamma','checkGE0')"
                  @focus="focusInputWithoutIndex('gamma','gamma','checkGE0')"
                  @blur="blurInputWithoutIndex('gamma','gamma','checkGE0')"
        ></el-input>
      </el-form-item>
      <!--  max_depth -->
      <el-form-item label="树的最大深度:">
        <el-input v-model="max_depth" :placeholder="max_depth_label" id="md"
                  @change="changeInputWithoutIndex('md','max_depth','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('md','max_depth','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('md','max_depth','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!--  min_child_weight -->
      <el-form-item label="最小实例权重:">
        <el-input v-model="min_child_weight" :placeholder="min_child_weight_label" id="mcwl"
                  @change="changeInputWithoutIndex('mcwl','min_child_weight','checkGE0')"
                  @focus="focusInputWithoutIndex('mcwl','min_child_weight','checkGE0')"
                  @blur="blurInputWithoutIndex('mcwl','min_child_weight','checkGE0')"
        ></el-input>
      </el-form-item>
      <!--  max_delta_step -->
      <el-form-item label="叶子输出最大增量步长:">
        <el-input v-model="max_delta_step" :placeholder="max_delta_step_label" id="mds"
                  @change="changeInputWithoutIndex('mds','max_delta_step','checkGE0')"
                  @focus="focusInputWithoutIndex('mds','max_delta_step','checkGE0')"
                  @blur="blurInputWithoutIndex('mds','max_delta_step','checkGE0')"
        ></el-input>
      </el-form-item>
      <!--  sub_sample -->
      <el-form-item label="子样本比率:">
        <el-input v-model="sub_sample" :placeholder="sub_sample_label" id="sub_sample"
                  @change="changeInputWithoutIndex('sub_sample','sub_sample','checkL02E1')"
                  @focus="focusInputWithoutIndex('sub_sample','sub_sample','checkL02E1')"
                  @blur="blurInputWithoutIndex('sub_sample','sub_sample','checkL02E1')"
        ></el-input>
      </el-form-item>
      <!--  lam -->
      <el-form-item label="L2正则化强度:">
        <el-input v-model="lam" :placeholder="lam_label" id="lam"
                  @change="changeInputWithoutIndex('lam','lam','checkGE0')"
                  @focus="focusInputWithoutIndex('lam','lam','checkGE0')"
                  @blur="blurInputWithoutIndex('lam','lam','checkGE0')"
        ></el-input>
      </el-form-item>
      <!--  sketch_eps -->
      <el-form-item label="分箱精度:">
        <el-input v-model="sketch_eps" :placeholder="sketch_eps_label" id="sketch_eps"
                  @change="changeInputWithoutIndex('sketch_eps','sketch_eps','checkL02S1')"
                  @focus="focusInputWithoutIndex('sketch_eps','sketch_eps','checkL02S1')"
                  @blur="blurInputWithoutIndex('sketch_eps','sketch_eps','checkL02S1')"
        ></el-input>
      </el-form-item>
      <!-- homomorphism -->
      <el-form-item label="同态加密算法:">
        <el-radio-group v-model="homomorphism">
          <el-radio label="cpaillier">cpaillier</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  key_size -->
      <el-form-item label="同态加密密钥长度:">
        <el-input v-model="key_size" :placeholder="key_size_label" id="key_size"
                  @change="changeInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('key_size','key_size','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <!-- importance_type -->
      <el-form-item label="特征重要性类型:">
        <el-radio-group v-model="importance_type">
          <el-radio label="weight">weight</el-radio>
          <el-radio label="gain">gain</el-radio>
          <el-radio label="cover">cover</el-radio>
          <el-radio label="total_gain">total_gain</el-radio>
          <el-radio label="total_cover">total_cover</el-radio>
          <el-radio label="all">all</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  train_validate_freq -->
      <el-form-item label="训练验证频率:">
        <el-input v-model="train_validate_freq" :placeholder="train_validate_freq_label"></el-input>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Dpgbdt",
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','45%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_dpgbdt');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      dpgbdtForm:{task_type:'dpgbdt',task_role:[],objective:[],num_round:[],eta:[],gamma:[],
        max_depth:[],min_child_weight:[],max_delta_step:[],sub_sample:[], lam:[],sketch_eps:[],homomorphism:[],
        key_size:[],train_validate_freq:[],importance_type:[]},
      //角色
      task_role:[],
      roleConfig:[],
      roleLabel:[],
      //其他参数
      objective: null,
      //num_round
      num_round: null, num_round_label: '请输入整数，取值范围[1,inf)', num_round_label_copy: '请输入整数，取值范围[1,inf)',
      num_round_time: true,
      //eta
      eta: null, eta_label: '请输入数值，取值范围(0,inf)', eta_label_copy: '请输入数值，取值范围(0,inf)', eta_time: true,
      //gamma
      gamma: null, gamma_label: '请输入数值，取值范围[0,inf)', gamma_label_copy: '请输入数值，取值范围[0,inf)', gamma_time: true,
      //max_depth
      max_depth: null, max_depth_label:'请输入整数，取值范围[1,inf)', max_depth_label_copy:'请输入整数，取值范围[1,inf)',
      max_depth_time: true,
      //min_child_weight
      min_child_weight: null, min_child_weight_label: '请输入数值，取值范围[0,inf)',
      min_child_weight_label_copy: '请输入数值，取值范围[0,inf)', min_child_weight_time: true,
      //max_delta_step
      max_delta_step: null, max_delta_step_label: '请输入数值，取值范围[0,inf)',
      max_delta_step_label_copy: '请输入数值，取值范围[0,inf)', max_delta_step_time: true,
      //sub_sample
      sub_sample: null, sub_sample_label: '请输入数值，取值范围(0,1]',
      sub_sample_label_copy: '请输入数值，取值范围(0,1]', sub_sample_time: true,
      //lam
      lam: null, lam_label: '请输入数值，取值范围[0, inf)', lam_label_copy: '请输入数值，取值范围[0, inf)', lam_time: true,
      //sketch_eps
      sketch_eps: null, sketch_eps_label: '请输入数值，取值范围(0,1)',
      sketch_eps_label_copy: '请输入数值，取值范围(0,1)', sketch_eps_time: true,
      homomorphism: null,
      //key_size
      key_size: null, key_size_label:'请输入整数，取值范围[1, inf)',
      key_size_label_copy:'请输入整数，取值范围[1, inf)', key_size_time:true,
      //train_validate_freq
      train_validate_freq: null, train_validate_freq_label: '请输入整数，取值范围[1, inf)',
      train_validate_freq_label_copy: '请输入整数，取值范围[1, inf)', train_validate_freq_time: true,
      importance_type: null,
      // 是否可见
      homomorphism_visible: false,
      key_size_visible: false,
      train_validate_freq_visible: false,
    };
  },
  methods: {
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //初始动态化化参数
    initPara(){
      // 动态初始化角色
      let role = this.$global.formatPartyPara(this.task_role, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, '任务角色:','role');
      this.task_role = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_dpgbdt',this.dpgbdtForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.dpgbdtForm;
      }else{
        this.moduleTable.push(this.dpgbdtForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.dpgbdtForm = {task_type:'dpgbdt',task_role:[],objective:[],num_round:[],eta:[],gamma:[],
        max_depth:[],min_child_weight:[],max_delta_step:[],sub_sample:[], lam:[],sketch_eps:[],homomorphism:[],
        key_size:[],train_validate_freq:[],importance_type:[]};
      this.dpgbdtForm.task_role = this.task_role;
      for(let i=0;i<this.$global.partyNumber;i++){
        this.dpgbdtForm.homomorphism.push(this.homomorphism);
        this.dpgbdtForm.key_size.push(parseInt(this.key_size));
        this.dpgbdtForm.train_validate_freq.push(parseInt(this.train_validate_freq));
      }
      for(let key in this.dpgbdtForm){
        if(key === 'task_type' || key === 'task_role'){
          continue;
        }
        if(this[key] !== null){
          if(this[key+'_visible']!==undefined && this[key+'_visible']===false){
            continue;
          }
          let keyInt = parseInt(this[key]);
          let keyFLoat = parseFloat(this[key]);
          let keyNum = keyInt;
          if(keyInt !== keyFLoat){
            keyNum = keyFLoat;
          }
          for(let i=0;i<this.$global.partyNumber;i++){
            if(isNaN(keyInt)){
              this.dpgbdtForm[key].push(this[key]);
            }else {
              this.dpgbdtForm[key].push(keyNum);
            }
          }
        }
      }
    },
    //从表单解析出来各个参数,在使用解析函数前，需要先执行initPara
    parsePara(form){
      for(let key in this.dpgbdtForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //如果存在key，则对其进行赋值，如果不存在key则已经在执行解析函数前执行了其他参数的初始化，不需要对其他参数进行处理
          if(key === 'task_type'){
            continue;
          }
          if(key === 'task_role'){
            this[key] = form[key];
            continue;
          }
          if(form[key]!==null && form[key].length > 0){
            this[key] = form[key][0];
          }
        }
      }
    },
    //清空表单
    clearForm(){
      this.dpgbdtForm = {task_type:'dpgbdt',task_role:[],objective:[],num_round:[],eta:[],gamma:[],
        max_depth:[],min_child_weight:[],max_delta_step:[],sub_sample:[], lam:[],sketch_eps:[],homomorphism:[],
        key_size:[],train_validate_freq:[],importance_type:[]};
      this.initPara();
      this.objective=null;
      this.num_round=null;
      this.eta= null;
      this.gamma= null;
      this.max_depth= null;
      this.min_child_weight= null;
      this.max_delta_step= null;
      this.sub_sample= null;
      this.lam= null;
      this.sketch_eps= null;
      this.homomorphism= null;
      this.key_size= null;
      this.train_validate_freq= null;
      this.importance_type= null;
      this.homomorphism_visible= false;
      this.key_size_visible= false;
      this.train_validate_freq_visible= false;
    },
    //对参数进行检查
    checkInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(this.$global[checkType](this[para_name]) || this[para_name]===''){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
        return true;
      }else{
        obj.style.borderColor = '#e30d28';
        this[para_name] = null;
        this[para_name+'_label'] = "参数错误! " + this[para_name+'_label_copy'];
        this.$forceUpdate();
        return false;
      }
    },
    //输入框发生改变的事件
    changeInputWithoutIndex(name, para_name,checkType){
      if(this[para_name+'_time']){
        this[para_name+'_time'] = false;
      }
      this.checkInputWithoutIndex(name, para_name, checkType);
    },
    //输入框被focus触发边框颜色变化
    focusInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        obj.style.borderColor = '#409EFF';
      }
    },
    //输入框被blur触发边框颜色变化
    blurInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
      }
    }
  }
}
</script>

