<template>
  <div class="neural_network">
    <el-form ref="data" :model="neuralNetworkForm" label-position="left" label-width="190px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
          <el-radio label="server">server</el-radio>
          <el-radio label="client">client</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  backend -->
      <el-form-item label="深度学习框架：">
        <el-radio-group v-model="backend">
          <el-radio label="keras">keras</el-radio>
          <el-radio label="pytorch">pytorch</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  format -->
      <el-form-item label="模型输入类型：">
        <el-radio-group v-model="format">
          <el-radio label="file">file</el-radio>
          <el-radio label="conf">conf</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  btm -->
      <el-form-item label="底层模型配置:">
        <el-input v-for="(item,index) in btm_config" :key="item.name"
                  v-model="btm[index]" :placeholder="btm_label[index]">
        </el-input>
      </el-form-item>
      <!--  mid -->
      <el-form-item label="中间层模型配置:">
        <el-input v-for="(item,index) in mid_config" :key="item.name"
                  v-model="mid[index]" :placeholder="mid_label[index]">
        </el-input>
      </el-form-item>
      <!--  top -->
      <el-form-item label="上层模型配置:">
        <el-input v-for="(item,index) in top_config" :key="item.name"
                  v-model="top[index]" :placeholder="top_label[index]">
        </el-input>
      </el-form-item>
      <!--  epochs -->
      <el-form-item label="epochs:">
        <el-input v-model="epochs" :placeholder="epochs_label" id="epochs"
                  @change="changeInputWithoutIndex('epochs','epochs','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('epochs','epochs','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('epochs','epochs','checkPositiveInteger')">
        </el-input>
      </el-form-item>
      <!--  batch_size -->
      <el-form-item label="batch大小:">
        <el-input v-model="batch_size" :placeholder="batch_size" id="batch_size"
                  @change="changeInputWithoutIndex('batch_size','batch_size','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('batch_size','batch_size','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('batch_size','batch_size','checkPositiveInteger')">
        </el-input>
      </el-form-item>
      <!-- loss_fn -->
      <el-form-item v-for="(item,index) in loss_fn_config" :key="item.name" :label="loss_fn_label[index]">
        <el-radio-group v-model="loss_fn[index]">
          <el-radio label="CrossEntropyLoss">CrossEntropyLoss</el-radio>
          <el-radio label="MSELoss">MSELoss</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  learning_rate -->
      <el-form-item label="学习率:">
        <el-input v-model="learning_rate" :placeholder="learning_rate_label" id="learning_rate"
                  @change="changeInputWithoutIndex('learning_rate','learning_rate','checkGE0')"
                  @focus="focusInputWithoutIndex('learning_rate','learning_rate','checkGE0')"
                  @blur="blurInputWithoutIndex('learning_rate','learning_rate','checkGE0')">
        </el-input>
      </el-form-item>
      <!--  optimizer -->
      <el-form-item label="优化器:">
        <el-radio-group v-model="optimizer">
          <el-radio label="SGD">SGD</el-radio>
          <el-radio label="Adam">Adam</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- use_mid -->
      <el-form-item label="是否使用中间层模型：">
        <el-switch v-model="use_mid" active-color="#13ce66"></el-switch>
      </el-form-item>
      <!--  mid_shape_in -->
      <el-form-item label="中间层模型输入的张量形状:" v-if="use_mid">
        <el-input v-model="mid_shape_in" :placeholder="mid_shape_in_label" id="mid_shape_in"
                  @change="changeInputWithoutIndex('mid_shape_in','mid_shape_in','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('mid_shape_in','mid_shape_in','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('mid_shape_in','mid_shape_in','checkPositiveInteger')">
        </el-input>
      </el-form-item>
      <!--  mid_shape_out -->
      <el-form-item label="中间层模型输入的张量形状:" v-if="use_mid">
        <el-input v-model="mid_shape_out" :placeholder="mid_shape_out_label" id="mid_shape_out"
                  @change="changeInputWithoutIndex('mid_shape_out','mid_shape_out','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('mid_shape_out','mid_shape_out','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('mid_shape_out','mid_shape_out','checkPositiveInteger')">
        </el-input>
      </el-form-item>
      <!--  mid_activation -->
      <el-form-item label="中间模型的激活函数：" v-if="use_mid">
        <el-radio-group v-model="mid_activation">
          <el-radio label="linear">linear</el-radio>
          <el-radio label="Relu">Relu</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- privacy_mode -->
      <el-form-item label="隐私模式:">
        <el-radio-group v-model="privacy_mode">
          <el-radio label="phe">phe</el-radio>
          <el-radio label="plain">plain</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  metrics -->
      <el-form-item v-for="(item,index) in metrics_config" :key="item.name" :label="metrics_label[index]">
        <el-radio-group v-model="metrics[index]">
          <el-radio label="accuracy">accuracy</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  predict_model -->
      <el-form-item v-for="(item,index) in predict_model_config" :key="item.name" :label="predict_model_label[index]">
        <el-radio-group v-model="predict_model[index]">
          <el-radio label="categorical">categorical</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  num_classes -->
      <el-form-item label="类别个数:">
        <el-input v-for="(item,index) in num_classes_config" :key="item.name" :id="item.name"
                  @change="changeInput(item.name,index,'num_classes','checkPositiveInteger')"
                  @focus="focusInput(item.name,index,'num_classes','checkPositiveInteger')"
                  @blur="blurInput(item.name,index,'num_classes','checkPositiveInteger')"
                  v-model="num_classes[index]" :placeholder="num_classes_label[index]">
        </el-input>
      </el-form-item>
      <!--  client_frac -->
      <el-form-item label="客户端占比:">
        <el-input v-for="(item,index) in client_frac_config" :key="item.name" :id="item.name"
                  @change="changeInput(item.name,index,'client_frac','checkL02E1')"
                  @focus="focusInput(item.name,index,'client_frac','checkL02E1')"
                  @blur="blurInput(item.name,index,'client_frac','checkL02E1')"
                  v-model="client_frac[index]" :placeholder="client_frac_label[index]">
        </el-input>
      </el-form-item>
      <!--  model_conf -->
      <el-form-item label="模型配置:">
        <el-input v-for="(item,index) in model_conf_config" :key="item.name"
                  v-model="model_conf[index]" :placeholder="model_conf_label[index]">
        </el-input>
      </el-form-item>
      <!--  train_validate_freq -->
      <el-form-item label="训练验证频率:">
        <el-input v-model="train_validate_freq" :placeholder="train_validate_freq_label" id="train_validate_freq"
                  @change="changeInputWithoutIndex('train_validate_freq','train_validate_freq','checkPositiveInteger')"
                  @focus="focusInputWithoutIndex('train_validate_freq','train_validate_freq','checkPositiveInteger')"
                  @blur="blurInputWithoutIndex('train_validate_freq','train_validate_freq','checkPositiveInteger')"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "NeuralNetwork",
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','40%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_neuralnetwork');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      neuralNetworkForm:{task_type:'neural_network', task_role:[], backend:[], format:[], btm:[], mid:[], top:[],
        epochs:[], batch_size:[], loss_fn:[], learning_rate:[],optimizer:[],use_mid:null,mid_shape_in:null,mid_shape_out:null,
        mid_activation:null,privacy_mode:[],metrics:[],predict_model:[],num_classes:[],client_frac:[],model_conf:[],
        train_validate_freq:[]},
      //角色
      task_role:[], roleConfig:[], roleLabel:[],
      //动态变化的参数
      btm:null, btm_config:null, btm_label:null,
      mid:null, mid_config:null, mid_label:null,
      top:null, top_config:null, top_label:null,
      loss_fn:null, loss_fn_config:null, loss_fn_label:null,
      metrics:null, metrics_config:null, metrics_label:null,
      predict_model:null, predict_model_config:null, predict_model_label: null,
      num_classes:null, num_classes_config:null, num_classes_label:null, num_classes_config_copy:null,num_classes_label_copy:null,
      client_frac:null, client_frac_config:null, client_frac_label:null,client_frac_config_copy:null,client_frac_label_copy:null,
      model_conf:null, model_conf_config:null, model_conf_label:null,
      //train_validate_freq
      train_validate_freq:null,train_validate_freq_label:'请输入整数，取值范围：[1, inf)',
      train_validate_freq_label_copy:'请输入整数，取值范围：[1, inf)',train_validate_freq_time:true,
      //其他一致的参数
      backend:null,
      format:null,
      epochs:null,epochs_label:'请输入整数，取值范围[1, inf)',epochs_label_copy:'请输入整数，取值范围[1, inf)',epochs_time:true,
      batch_size:null,batch_size_label:'请输入整数，取值范围[1, inf)',
      batch_size_label_copy:'请输入整数，取值范围[1, inf)',batch_size_time:true,
      learning_rate:null,learning_rate_label:'请输入数值，取值范围：[0, inf)',
      learning_rate_label_copy:'请输入数值，取值范围：[0, inf)',learning_rate_time:true,
      optimizer:null,
      privacy_mode:null,
      //单个的参数
      use_mid:null,
      mid_shape_in:null,mid_shape_in_label:'请输入整数,取值范围:[1, inf)',
      mid_shape_in_label_copy:'请输入整数,取值范围:[1, inf)',mid_shape_in_time:true,
      mid_shape_out:null,mid_shape_out_label:'请输入整数,取值范围:[1, inf)',
      mid_shape_out_label_copy:'请输入整数,取值范围:[1, inf)',mid_shape_out_time:true,
      mid_activation:null,
    }
  },
  methods: {
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //初始动态化化参数
    initPara(){
      // 动态初始化角色
      let re = this.$global.formatPartyPara(this.task_role, this.roleConfig,
          this.roleLabel, this.$global.partyNumber, '角色:', 'role');
      this.task_role = re[0];
      this.roleConfig = re[1];
      this.roleLabel = re[2];
      // 动态初始化btm
      re = this.$global.formatPartyPara(this.btm, this.btm_config,
          this.btm_label, this.$global.partyNumber, ',请输入json字符串或模型所在的路径', 'btm');
      this.btm = re[0];
      this.btm_config = re[1];
      this.btm_label = re[2];
      // 动态初始化 mid
      re = this.$global.formatPartyPara(this.mid, this.mid_config,
          this.mid_label, this.$global.partyNumber, ',请输入json字符串或模型所在的路径', 'mi');
      this.mid = re[0];
      this.mid_config = re[1];
      this.mid_label = re[2];
      // 动态初始化 top
      re = this.$global.formatPartyPara(this.top, this.top_config,
          this.top_label, this.$global.partyNumber, ',请输入json字符串或模型所在的路径', 'to');
      this.top = re[0];
      this.top_config = re[1];
      this.top_label = re[2];
      // 动态初始化 loss_fn
      re = this.$global.formatPartyPara(this.loss_fn, this.loss_fn_config,
          this.loss_fn_label, this.$global.partyNumber, '损失函数:', 'loss');
      this.loss_fn = re[0];
      this.loss_fn_config = re[1];
      this.loss_fn_label = re[2];
      // 动态初始化 metrics
      re = this.$global.formatPartyPara(this.metrics, this.metrics_config,
          this.metrics_label, this.$global.partyNumber, '输出矩阵:', 'metrics');
      this.metrics = re[0];
      this.metrics_config = re[1];
      this.metrics_label = re[2];
      // 动态初始化 predict_model
      re = this.$global.formatPartyPara(this.predict_model, this.predict_model_config,
          this.predict_model_label, this.$global.partyNumber, '预测模型:', 'predict');
      this.predict_model = re[0];
      this.predict_model_config = re[1];
      this.predict_model_label = re[2];
      // 动态初始化 num_classes
      re = this.$global.formatPartyPara(this.num_classes, this.num_classes_config,
          this.num_classes_label, this.$global.partyNumber, '请输入整数，取值范围[1, inf)', 'classes');
      this.num_classes = re[0];
      this.num_classes_config = re[1];
      this.num_classes_label = re[2];
      this.num_classes_config_copy = this.$global.copyJson(this.num_classes_config);
      this.num_classes_label_copy = this.$global.copyJson(this.num_classes_label);
      // 动态初始化 client_frac
      re = this.$global.formatPartyPara(this.client_frac, this.client_frac_config,
          this.client_frac_label, this.$global.partyNumber, '请输入数值，取值范围：(0,1]', 'frac');
      this.client_frac = re[0];
      this.client_frac_config = re[1];
      this.client_frac_label = re[2];
      this.client_frac_config_copy = this.$global.copyJson(this.client_frac_config);
      this.client_frac_label_copy = this.$global.copyJson(this.client_frac_label);
      // 动态初始化 model_conf
      re = this.$global.formatPartyPara(this.model_conf, this.model_conf_config,
          this.model_conf_label, this.$global.partyNumber, '请输入模型配置json字符串或模型地址', 'conf');
      this.model_conf = re[0];
      this.model_conf_config = re[1];
      this.model_conf_label = re[2];
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_neuralnetwork',this.neuralNetworkForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.neuralNetworkForm;
      }else{
        this.moduleTable.push(this.neuralNetworkForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.neuralNetworkForm = {task_type:'neural_network', task_role:[], backend:[], format:[], btm:[], mid:[], top:[],
        epochs:[], batch_size:[], loss_fn:[], learning_rate:[],optimizer:[],use_mid:null,mid_shape_in:null,mid_shape_out:null,
        mid_activation:null,privacy_mode:[],metrics:[],predict_model:[],num_classes:[],client_frac:[],model_conf:[],
        train_validate_freq:[]};
      this.neuralNetworkForm.task_role = this.task_role;
      for(let i=0;i<this.$global.partyNumber;i++){
        this.neuralNetworkForm.train_validate_freq.push(parseInt(this.train_validate_freq));
      }
      for(let key in this.neuralNetworkForm){
        if(key === 'task_type' || key === 'task_role' || key === 'train_validate_freq'){
          continue;
        }
        if(key === 'use_mid' || key === 'mid_shape_in' || key === 'mid_shape_out' || key === 'mid_activation'
            || key === 'btm' || key === 'mid' || key ==='top' || key === 'loss_fn' || key === 'metrics'
            || key === 'predict_model' || key === 'num_classes' || key === 'client_frac' || key === 'model_conf'){
          this.neuralNetworkForm[key] = this.$global.parseStrArray2IntArray(this[key]);
          continue;
        }
        if(this[key] !== null){
          if(this[key+'_visible']!==undefined && this[key+'_visible']===false){
            continue;
          }
          let keyInt = parseInt(this[key]);
          let keyFLoat = parseFloat(this[key]);
          let keyNum = keyInt;
          if(keyInt !== keyFLoat){
            keyNum = keyFLoat;
          }
          for(let i=0;i<this.$global.partyNumber;i++){
            if(isNaN(keyInt)){
              this.neuralNetworkForm[key].push(this[key]);
            }else {
              this.neuralNetworkForm[key].push(keyNum);
            }
          }
        }
      }
    },
    //从表单解析出来各个参数,在使用解析函数前，需要先执行initPara
    parsePara(form){
      for(let key in this.neuralNetworkForm){
        // eslint-disable-next-line no-prototype-builtins
        if(form.hasOwnProperty(key)){
          //如果存在key，则对其进行赋值，如果不存在key则已经在执行解析函数前执行了其他参数的初始化，不需要对其他参数进行处理
          if(key === 'task_type'){
            continue;
          }
          if(key === 'task_role'){
            this[key] = form[key];
            continue;
          }
          if(key === 'use_mid' || key === 'mid_shape_in' || key === 'mid_shape_out' || key === 'mid_activation'
              || key === 'btm' || key === 'mid' || key ==='top' || key === 'loss_fn' || key === 'metrics'
              || key === 'predict_model' || key === 'num_classes' || key === 'client_frac' || key === 'model_conf'){
            this[key] = form[key];
            continue;
          }
          if(form[key]!==null && form[key].length > 0){
            this[key] = form[key][0];
          }
        }
      }
    },
    //清空表单
    clearForm(){
      this.neuralNetworkForm = {task_type:'neural_network', task_role:[], backend:[], format:[], btm:[], mid:[], top:[],
        epochs:[], batch_size:[], loss_fn:[], learning_rate:[],optimizer:[],use_mid:null,mid_shape_in:null,mid_shape_out:null,
        mid_activation:null,privacy_mode:[],metrics:[],predict_model:[],num_classes:[],client_frac:[],model_conf:[],
        train_validate_freq:[]};
      this.initPara();
      this.backend=null;
      this.format=null;
      this.epochs=null;
      this.batch_size=null;
      this.learning_rate=null;
      this.optimizer=null;
      this.privacy_mode=null;
      this.use_mid=null;
      this.mid_shape_in=null;
      this.mid_shape_out=null;
      this.mid_activation=null;
      this.train_validate_freq=null;
    },
    //对参数进行检查
    checkInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(this.$global[checkType](this[para_name]) || this[para_name]===''){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
        return true;
      }else{
        obj.style.borderColor = '#e30d28';
        this[para_name] = null;
        this[para_name+'_label'] = "参数错误! " + this[para_name+'_label_copy'];
        this.$forceUpdate();
        return false;
      }
    },
    //输入框发生改变的事件
    changeInputWithoutIndex(name, para_name,checkType){
      if(this[para_name+'_time']){
        this[para_name+'_time'] = false;
      }
      this.checkInputWithoutIndex(name, para_name, checkType);
    },
    //输入框被focus触发边框颜色变化
    focusInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        obj.style.borderColor = '#409EFF';
      }
    },
    //输入框被blur触发边框颜色变化
    blurInputWithoutIndex(name, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_time'] && this.checkInputWithoutIndex(name,para_name,checkType)){
        this[para_name+'_label'] = this[para_name+'_label_copy'];
        obj.style.borderColor = '#DCDFE6';
      }
    },
    //对参数进行检查
    checkInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(this.$global[checkType](this[para_name][index]) || this[para_name][index]===''){
        this[para_name+'_label'][index] = this[para_name+'_label_copy'][index];
        obj.style.borderColor = '#DCDFE6';
        return true;
      }else{
        obj.style.borderColor = '#e30d28';
        this[para_name][index] = null;
        this[para_name+'_label'][index] = "参数错误! " + this[para_name+'_label_copy'][index];
        this.$forceUpdate();
        return false;
      }
    },
    //输入框发生改变的事件
    changeInput(name, index, para_name,checkType){
      if(this[para_name+'_config_copy'][index].isFirstTime){
        this[para_name+'_config_copy'][index].isFirstTime = false;
      }
      this.checkInput(name, index, para_name,checkType);
    },
    //输入框被focus触发边框颜色变化
    focusInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_config_copy'][index].isFirstTime && this.checkInput(name,index,para_name,checkType)){
        obj.style.borderColor = '#409EFF';
      }
    },
    //输入框被blur触发边框颜色变化
    blurInput(name, index, para_name,checkType){
      let obj = document.getElementById(name);
      if(!this[para_name+'_config_copy'][index].isFirstTime && this.checkInput(name,index,para_name,checkType)){
        this[para_name+'_label'][index] = this[para_name+'_label_copy'][index];
        obj.style.borderColor = '#DCDFE6';
      }
    }
  }
}
</script>

