<template>
  <div class="data_loader">
    <el-form ref="data" :model="dataLoaderForm" label-position="left" label-width="170px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in partyConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="task_role[index]">
          <el-tooltip content="guest侧数据需要带标签" placement="top" effect="light">
            <el-radio label="guest">guest</el-radio>
          </el-tooltip>
          <el-radio label="host">host</el-radio>
          <el-radio label="sole">sole</el-radio>
          <el-radio label="slack">slack</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- input data source -->
      <el-form-item v-for="(item,index) in input_data_source_config" :key="item.name" :label="input_data_source_label[index]">
        <el-radio-group v-model="input_data_source[index]">
          <el-radio label="csv">csv</el-radio>
          <el-radio label="hdfs">hdfs</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--   train and validate   -->
      <el-form-item label="是否有验证数据:">
        <el-switch v-model="is_train_validate" active-color="#13ce66"></el-switch>
      </el-form-item>
      <!-- input data path -->
      <el-form-item label="要加载的数据路径:" v-if="!is_train_validate">
        <el-input v-for="(item,index) in input_data_path_config" :key="item.name"
                  v-model="input_data_path[index]" :placeholder="input_data_path_PlaceHolder[index]">
        </el-input>
      </el-form-item>
      <!-- train_data_path -->
      <el-form-item label="要训练的数据路径:" v-if="is_train_validate">
        <el-input v-for="(item,index) in train_data_path_config" :key="item.name"
                  v-model="train_data_path[index]" :placeholder="train_data_path_PlaceHolder[index]">
        </el-input>
      </el-form-item>
      <!-- validate_data_path -->
      <el-form-item label="要验证的数据路径:" v-if="is_train_validate">
        <el-input v-for="(item,index) in validate_data_path_config" :key="item.name"
                  v-model="validate_data_path[index]" :placeholder="validate_data_path_PlaceHolder[index]">
        </el-input>
      </el-form-item>
      <!--   convert_sparse_to_index   -->
      <el-form-item label="是否将稀疏特征转自然数:">
        <el-switch v-model="convert_sparse_to_index" active-color="#13ce66"></el-switch>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'DataLoader',
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','40%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_dataloader');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      input_data_source:[],
      input_data_source_config:[],
      input_data_source_label:[],
      input_data_path:[],
      input_data_path_config:[],
      input_data_path_PlaceHolder:[],
      train_data_path:[],
      train_data_path_config:[],
      train_data_path_PlaceHolder:[],
      validate_data_path:[],
      validate_data_path_config:[],
      validate_data_path_PlaceHolder:[],
      data_carrier:null,
      is_train_validate: false,
      dataLoaderForm:{task_type: 'data_loader',task_role: [],input_data_source: [],
        input_data_path: [],convert_sparse_to_index:null,train_data_path:[],validate_data_path:[]},
      task_role:[],
      partyConfig:[],
      roleLabel:[],
      convert_sparse_to_index:true,
    };
  },
  methods: {
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_dataloader',this.dataLoaderForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.dataLoaderForm;
      }else{
        this.moduleTable.push(this.dataLoaderForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.dataLoaderForm.task_role = this.task_role;
      this.dataLoaderForm.input_data_source = this.input_data_source;
      this.isTrainValidate();
      if(this.is_train_validate){
        this.dataLoaderForm.train_data_path = this.train_data_path;
        this.dataLoaderForm.validate_data_path = this.validate_data_path;
      }else {
        this.dataLoaderForm.input_data_path = this.input_data_path;
      }
      let convert = [];
      for(let i=0;i<this.$global.partyNumber;i++){
        convert.push(this.convert_sparse_to_index);
      }
      this.dataLoaderForm.convert_sparse_to_index = convert;
    },
    //从表单解析出来各个参数
    parsePara(dataLoaderForm){
      this.task_role = dataLoaderForm.task_role;
      this.input_data_source = dataLoaderForm.input_data_source;
      if(typeof(dataLoaderForm.input_data_path) !== 'undefined'){
        this.input_data_path = dataLoaderForm.input_data_path;
        this.is_train_validate = false;
      }else{
        this.is_train_validate = true;
      }
      if(typeof(dataLoaderForm.train_data_path) !== 'undefined' &&
          typeof(dataLoaderForm.validate_data_path) !== 'undefined'){
        this.train_data_path = dataLoaderForm.train_data_path;
        this.validate_data_path = dataLoaderForm.validate_data_path;
        this.is_train_validate = true;
      }else{
        this.is_train_validate = false;
      }
      if(this.input_data_path.length!==0){
        for(let i=0;i<this.input_data_path.length;i++){
          if(this.input_data_path[i]!==null){
            this.is_train_validate = false;
          }
        }
      }
      if(this.train_data_path.length === 0 && this.validate_data_path.length === 0){
        this.is_train_validate = false;
      }
      // eslint-disable-next-line no-prototype-builtins
      if(typeof(dataLoaderForm.convert_sparse_to_index) !== 'undefined'){
        if(dataLoaderForm.convert_sparse_to_index === null){
          this.convert_sparse_to_index = true;
        }else {
          this.convert_sparse_to_index = dataLoaderForm.convert_sparse_to_index[0];
        }
      }
    },
    //是否加载训练和验证数据集
    isTrainValidate(){
      if(this.is_train_validate){
        // 动态初始化input_data_path
        let path = this.$global.formatPartyPara(this.input_data_path, this.input_data_path_config,
            this.input_data_path_PlaceHolder, this.$global.partyNumber, '的数据路径', 'path')
        this.input_data_path = path[0];
        this.input_data_path_config = path[1];
        this.input_data_path_PlaceHolder = path[2];
      }else{
        // train_data_path
        let train = this.$global.formatPartyPara(this.train_data_path, this.train_data_path_config,
            this.train_data_path_PlaceHolder, this.$global.partyNumber, '的训练数据路径', 'path')
        this.train_data_path = train[0];
        this.train_data_path_config = train[1];
        this.train_data_path_PlaceHolder = train[2];
        // validate_data_path
        let validate = this.$global.formatPartyPara(this.validate_data_path, this.validate_data_path_config,
            this.validate_data_path_PlaceHolder, this.$global.partyNumber, '的验证数据路径', 'path')
        this.validate_data_path = validate[0];
        this.validate_data_path_config = validate[1];
        this.validate_data_path_PlaceHolder = validate[2];
      }
    },
    //清空表单
    clearForm(){
      this.dataLoaderForm = {task_type: 'data_loader',task_role: [],input_data_source: [],
        input_data_path: [],convert_sparse_to_index:null,train_data_path:[],validate_data_path:[]};
      this.initPara();
      this.convert_sparse_to_index=true;
      this.is_train_validate = false;
    },
    //动态初始化参数
    initPara(){
      // 动态初始化角色
      let role = this.$global.formatPartyPara(this.task_role, this.partyConfig,
          this.roleLabel, this.$global.partyNumber, '角色:', 'role');
      this.task_role = role[0];
      this.partyConfig = role[1];
      this.roleLabel = role[2];
      // 动态初始化input_data_source
      let source = this.$global.formatPartyPara(this.input_data_source, this.input_data_source_config,
          this.input_data_source_label, this.$global.partyNumber, '数据类型:', 'source')
      this.input_data_source = source[0];
      this.input_data_source_config = source[1];
      this.input_data_source_label = source[2];
      // 动态初始化input_data_path
      let path = this.$global.formatPartyPara(this.input_data_path, this.input_data_path_config,
          this.input_data_path_PlaceHolder, this.$global.partyNumber, '的数据路径', 'path')
      this.input_data_path = path[0];
      this.input_data_path_config = path[1];
      this.input_data_path_PlaceHolder = path[2];
      // train_data_path
      let train = this.$global.formatPartyPara(this.train_data_path, this.train_data_path_config,
          this.train_data_path_PlaceHolder, this.$global.partyNumber, '的训练数据路径', 'path')
      this.train_data_path = train[0];
      this.train_data_path_config = train[1];
      this.train_data_path_PlaceHolder = train[2];
      // validate_data_path
      let validate = this.$global.formatPartyPara(this.validate_data_path, this.validate_data_path_config,
          this.validate_data_path_PlaceHolder, this.$global.partyNumber, '的验证数据路径', 'path')
      this.validate_data_path = validate[0];
      this.validate_data_path_config = validate[1];
      this.validate_data_path_PlaceHolder = validate[2];
    },
  }
}
</script>

