<template>
  <div class="task_new">
    <!-- breadcrum -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/task' }">任务管理</el-breadcrumb-item>
      <el-breadcrumb-item>发起任务</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row>
        <el-col :span="1.5">
          <span class="demonstration">参与方个数：</span>
        </el-col>
        <el-col :span="3">
          <el-input class="partyNumberInput" v-model="partyNumber" placeholder="请输入整数"
                    v-on:input="changePartyNumber" @change="checkPartyNumber">
            <el-button slot="append" icon="el-icon-s-operation" @click="showPartyNames = !showPartyNames"></el-button>
          </el-input>
        </el-col>
        <el-col :span="1"><span>&nbsp;</span></el-col>
        <el-col :span="1.5">
          <span class="demonstration">是否保存模型：</span>
        </el-col>
        <el-col :span="2">
          <el-switch v-model="taskChainForm.save_model" active-color="#13ce66" @change="saveTaskChain"></el-switch>
        </el-col>
        <el-col :span="1.5">
          <span class="demonstration">消息类型：</span>
        </el-col>
        <el-col :span="5">
          <el-radio-group v-model="task_chain_messenger_type" @change="handleMessengerType">
            <el-radio :label="'kafka'">kafka</el-radio>
            <el-radio :label="'hdfs'">hdfs</el-radio>
            <el-radio :label="'alluxio'">alluxio</el-radio>
          </el-radio-group>
        </el-col>
        <el-col :span="1.5">
          <span class="demonstration">消息服务器地址：</span>
        </el-col>
        <el-col :span="4">
          <el-input class="partyNumberInput" v-model="task_chain_messenger_server"
                    @change="handleMessengerServer" placeholder="地址之间用逗号分割"></el-input>
        </el-col>
      </el-row>
    </el-card>
    <el-row v-if="showPartyNames" v-model="task_chain_party_names">
      <el-col :span="6" v-for="party in partyConfig" :key="party.name">
        <el-card class="partyNameCard">
          <el-col :span="10">
            <span class="demonstration">第{{party.name}}个参与方名称：</span>
          </el-col>
          <el-col :span="13">
            <el-input class="partyNumberInput" v-model="task_chain_party_names[party.name-1]"
                      placeholder="请输入参与方名称" @change="handlePartyNames"></el-input>
          </el-col>
        </el-card>
      </el-col>
    </el-row>
    <!-- task card -->
    <el-card class="box-card">
      <!-- add module & start task button -->
      <el-row>
        <el-cascader placeholder="请选择要添加的模块" v-model="currentCascader" :options="modules"
                     :show-all-levels="false" :props="{ expandTrigger: 'hover'}" @change="cascaderChange"></el-cascader>
        <!--        <el-button type="primary" @click="addModule">测试模块</el-button>-->
        <el-button type="info" @click="editInJson">
          JSON模式
          <i class="el-icon-tickets"></i>
        </el-button>
        <el-button type="success" @click="submitTask">
          发起任务
          <i class="el-icon-s-promotion"></i>
        </el-button>
      </el-row>
      <!-- task table -->
      <el-table
          :data="moduleTable"
          stripe
          style="width: 100%">
        <!-- row expansion & can see more info of the added module -->
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left">
              <el-form-item v-for="(value, key) in props.row" :key="key" :label="key+':' ">
                <span>{{ value }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <!-- column 1: index -->
        <el-table-column type="index"> </el-table-column>
        <!-- column 2: task type -->
        <el-table-column label="模块名称">
          <template v-slot="scope">
            {{getTaskType(scope.row['task_type'])}}
          </template>
        </el-table-column>
        <!-- column 3: task class -->
        <el-table-column label="模块类别">
          <template v-slot="scope">
            {{getClassification(scope.row['task_type'])}}
          </template>
        </el-table-column>
        <!-- column 4: task description -->
        <el-table-column label="模块描述">
          <template v-slot="scope">
            {{getDescription(scope.row['task_type'])}}
          </template>
        </el-table-column>
        <!-- column 5: operation -->
        <el-table-column label="模块操作" >
          <template v-slot="scope">
            <!-- tip for modify button -->
            <el-tooltip content="修改" placement="top" :enterable="false">
              <el-button type="primary" icon="el-icon-edit" @click="editModule(scope)" size="mini"></el-button>
            </el-tooltip>
            <!-- tip for delete button -->
            <el-tooltip content="删除" placement="top" :enterable="false">
              <el-button type="danger" icon="el-icon-delete" @click="deleteModule(scope)" size="mini"></el-button>
            </el-tooltip>
            <el-tooltip content="Json模式" placement="top" :enterable="false">
              <el-button type="info" icon="el-icon-tickets" @click="jsonModule(scope)" size="mini"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!-- add&edit module dialog -->
    <div v-if="moduleDialogVisible">
      <el-dialog
          v-bind:title="getTaskType(currentModule)"
          :visible="moduleDialogVisible"
          :close-on-click-modal='false'
          :before-close="moduleDialogClose"
          :width="dialogWidth">
			<span>
				<component :is="elementComponet[indexOfCurrentAddModule]" @setVisible="setVisible"
                   @setDialogWidth="setDialogWidth"></component>
			</span>
      </el-dialog>
    </div>
    <!-- edit json dialog -->
    <div v-if="editJsonDialogVisible">
      <el-dialog
          title="Json模式"
          :visible="editJsonDialogVisible"
          :close-on-click-modal='false'
          :before-close="cancelEdit"
          width="50%">
        <vue-json-editor
            v-model="moduleTable"
            mode="code"
            lang="zh"
        />
        <span slot="footer" class="dialog-footer">
				<el-button type="primary" @click="confirmEdit">修改</el-button>
        <el-button type="info" @click="cancelEdit">取消</el-button>
			</span>
      </el-dialog>
    </div>
    <!-- edit module json dialog -->
    <el-dialog
        title="Json模块修改"
        :visible="editJsonModuleDialogVisible"
        :close-on-click-modal='false'
        :before-close="cancelEdit"
        width="50%">
      <span>
        <!--   v-if is used to load the mounted function every time in component when the dialog is open     -->
        <component :is="elementComponet[14]" v-if="editJsonModuleDialogVisible" v-bind:data="moduleTable"></component>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmEdit">修改</el-button>
        <el-button type="info" @click="cancelEdit">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
// 组件
import DataLoader from "./TaskModule/DataLoader";
import DataSaver from "./TaskModule/DataSaver";
import Aligner from "./TaskModule/Aligner";
import Bucketizer from "./TaskModule/Bucketizer";
import Imputation from "./TaskModule/Imputation";
import MinMaxScaler from "./TaskModule/MinMaxScaler";
import OneHotEncoder from "./TaskModule/OneHotEncoder";
import Glm from "./TaskModule/Glm";
import Dpgbdt from "./TaskModule/Dpgbdt";
import RandomForest from "./TaskModule/RandomForest";
import Evaluate from "./TaskModule/Evaluate";
import ModelLoader from "./TaskModule/ModelLoader";
import PredictOffline from "./TaskModule/PredictOffline";
import PrivatePublish from "./TaskModule/PrivatePublish";
import Unknown from "./TaskModule/Unknown";
import JsonEditor from "./TaskModule/JsonEditor";
import vueJsonEditor from 'vue-json-editor';
import IV from './TaskModule/IV'
import KMeans from "./TaskModule/KMeans";
import NeuralNetwork from "./TaskModule/NeuralNetwork";
import Vue from "vue";

export default {
  name: 'TaskNew',
  components: {
    vueJsonEditor,
  },
  data() {
    return {
      // 1.common part
      // the modules added are shown in muduleTable
      moduleTable: [],
      submitTable: [],
      // the module options can be choosed
      modules: [{
        value: 'data_load', label: '数据加载存储',
        children: [{value: 'data_loader', label: '数据加载 | data loader'},
          {value: 'data_saver',label: '数据存储 | data saver'}]
      },{
        value: 'aligner', label: '样本对齐模块',
        children: [{value: 'aligner',label: '样本对齐 | aligner'}]
      },{
        value: 'federated_learning', label: '模型训练模块',
        children: [{value: 'dpgbdt',label: '差分隐私梯度提升决策树 | dpgbdt'}, {value: 'glm',label: '广义线性模型 | glm'},
          {value: 'neural_network', label: '神经网络 | neural network'}]
      },{
        value: 'predict', label: '预测评估模块',
        children: [{value: 'evaluate',label: '模型评估 | evaluate'},
          {value: 'model_loader',label: '模型加载卸载 | model loader'},
          {value: 'predict_offline',label: '离线预测 | predict offline'}]
      }],
      moduleDialogVisible: false,
      editDialogVisible: false,
      editJsonDialogVisible:false,
      editJsonModuleDialogVisible: false,
      //temp_json_table用户存储临时的json表单，用于取消json修改时回复到修改前的状态
      temp_json_table:[],
      // current added module, which is corresponding with the indexOfCurrentAddModule
      currentModule: '',
      currentEditModule: '',
      currentCascader: [],
      indexOfCurrentAddModule: -1,
      indexOfCurrentEditModule: -1,
      indexOfModuleTable: -1,
      // tempData to store data read form scope when edit module
      partyConfig:[],
      partyNumber: null,
      showPartyNames: true,
      task_chain_messenger_server: null,
      task_chain_party_names:[],
      task_chain_messenger_type:'',
      dialogWidth: '50%',
      // 2. data for all modules
      allModules: ['data_loader', 'data_saver', 'aligner', 'bucketizer', 'imputation','min_max_scaler',
        'one_hot_encoder','glm','dpgbdt','random_forest', 'evaluate', 'model_loader','predict_offline',
        'private_publish','json_editor','iv', 'kmeans', 'neural_network','unknown'],
      // index for modules, which is corresponding with the index in allModules
      indexModules: {data_loader: 0, data_saver: 1, aligner:2, bucketizer:3, imputation:4, min_max_scaler:5,
        one_hot_encoder:6, glm:7, dpgbdt:8, random_forest:9, evaluate:10, model_loader:11, predict_offline:12,
        private_publish:13,json_editor:14, iv:15, kmeans:16, neural_network:17},
      nameModules:{},
      taskChainForm:{task_type: 'task_chain', party_names: [], messenger_server: [],
        save_model: true, messenger_type: []},
    }
  },
  computed: {
    elementComponet: function () {
      return [DataLoader,DataSaver,Aligner,Bucketizer,Imputation,MinMaxScaler,OneHotEncoder,Glm,Dpgbdt,RandomForest,
        Evaluate,ModelLoader,PredictOffline,PrivatePublish,JsonEditor,IV,KMeans,NeuralNetwork,Unknown];
    }
  },
  mounted() {
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    if(this.moduleTable === null){
      this.moduleTable = [];
    }
    if(this.moduleTable.length !== 0 && this.moduleTable[0] === null){
      this.moduleTable = [];
    }
    this.partyNumber = this.$global.partyNumber;
    this.taskchain = this.$global.readLocalStorage('jeddak_task_chain');
    if(this.taskchain !== null && this.taskchain.party_names.length !== 0){
      this.taskChainForm = this.taskchain;
      this.$global.taskChainForm = this.taskChainForm;
      this.parseTaskChain(this.taskChainForm);
    }else if(this.$global.taskChainForm !== null && this.$global.taskChainForm.party_names.length !== 0){
      this.taskChainForm = this.$global.taskChainForm;
      this.parseTaskChain(this.taskChainForm);
    }
  },
  methods: {
    // 1. common part
    // sascader change
    cascaderChange() {
      this.currentModule = this.currentCascader[1];
      this.currentCascader = []
      this.addModule();
    },
    getSubmitTable(){
      this.submitTable=[];
      this.moduleTable[0] = this.taskChainForm;
      for(let item of this.moduleTable){
        let m = {};
        for(let prop in item){
          // eslint-disable-next-line no-prototype-builtins
          if(item.hasOwnProperty(prop)){
            if(item[prop].length !==0 && item[prop]!==''){
              m[prop] = item[prop];
            }
          }
        }
        this.submitTable.push(m);
      }
      window.localStorage.setItem('jeddak_module_table',JSON.stringify(this.submitTable))
    },
    submitTask() {
      // submit to backend
      this.$notify.success({
        title: '提示',
        message: '任务提交中，请稍候'
      });
      this.loading = true;
      this.getSubmitTable();
      this.$axios.post("/task_chain/submit", this.submitTable, {
        headers: {
          'auth': window.localStorage.getItem('jeddak_token')
        }
      }).then(resp => {
        console.log(resp);
        this.loading = false;
        this.$notify.success({
          title: '提示',
          message: '任务提交已完成，请在任务管理页面查看详细信息'
        });
      }).catch(err => {
        console.log(err);
        this.$notify.error({
          title: '提示',
          message: '任务提交失败，请检查参数配置是否正确或确认是否具备权限'
        });
        this.loading = false;
      });
    },
    //Json模式：修改
    confirmEdit(){
      if(this.moduleTable!==null && this.moduleTable.length!==0 && this.moduleTable[0].task_type === 'task_chain'){
        this.parseTaskChain(this.moduleTable[0]);
      }else if(this.moduleTable === null){
        this.$global.partyNumber = 0;
        this.partyNumber = 0;
        this.initTaskChain();
      }else if(this.moduleTable.length === 0){
        this.$global.partyNumber = 0;
        this.partyNumber = 0;
        this.initTaskChain();
      }
      this.moduleTable[0] = this.taskChainForm;
      window.localStorage.setItem("jeddak_module_table", JSON.stringify(this.moduleTable));
      this.editJsonDialogVisible = false;
      this.editJsonModuleDialogVisible = false;
    },
    //Json模式：取消
    cancelEdit(){
      this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
      this.moduleTable[0] = this.taskChainForm;
      this.editJsonDialogVisible = false;
      this.editJsonModuleDialogVisible = false;
    },
    //Json模式
    editInJson(){
      this.editJsonDialogVisible = true;
    },
    // 2. methods need to be set for all modules
    // get classification of module table
    getTaskType(t){
      //与allModules对应：['data_loader', 'data_saver', 'aligner', 'bucketizer', 'imputation','min_max_scaler',
      // 'one_hot_encoder','glm','dpgbdt','random_forest', 'evaluate',
      // 'model_loader','predict_offline','private_publish','json_editor','iv', 'kmeans', 'neural_network','unknown']
      if(t==='task_chain'){return "任务链 | task chain"}
      if(t==='linear_regression'){return "线性回归 | linear regression"}
      if(t==='logistic_regression'){return "逻辑回归 | logistic regression"}
      if(t==='poisson_regression'){return "泊松回归 | poisson regression"}
      let ans = ["数据加载 | data loader","数据存储 | data saver","样本对齐 | aligner","数据分桶 | bucketizer",
        "缺失值填充 | imputation","归一化 | min max scaler","独热编码 | one hot encoder", "广义线性回归 | glm",
        "差分隐私梯度提升决策树 | dpgbdt","随机森林 | random forest","评估 | evaluate","模型加载卸载 | model loader",
        "离线预测 | predict offline","隐私发布 | private publish","Json模式","信息值 | IV",
        "K均值聚类 | K-means","神经网络 | neural network",""];
      return ans[this.indexModules[t]]
    },
    getClassification(c) {
      //与allModules对应：['data_loader', 'data_saver', 'aligner', 'bucketizer', 'imputation','min_max_scaler',
      // 'one_hot_encoder','glm','dpgbdt','random_forest', 'evaluate',
      // 'model_loader','predict_offline','private_publish','json_editor','iv', 'kmeans', 'neural_network','unknown']
      if(c==='task_chain'){return "任务链模块"}
      if(c==='linear_regression'){return "模型训练模块"}
      if(c==='logistic_regression'){return "模型训练模块"}
      if(c==='poisson_regression'){return "模型训练模块"}
      let ans = ["数据加载存储","数据加载存储","样本对齐模块","特征工程模块","特征工程模块","特征工程模块","特征工程模块",
        "模型训练模块","模型训练模块","模型训练模块","预测评估模块","预测评估模块","预测评估模块","预测评估模块","Json模式","特征工程模块",
        "特征工程模块","模型训练模块",""];
      return ans[this.indexModules[c]]
    },
    // get description of module table
    getDescription(d) {
      //与allModules对应：['data_loader', 'data_saver', 'aligner', 'bucketizer', 'imputation','min_max_scaler',
      // 'one_hot_encoder','glm','dpgbdt','random_forest', 'evaluate',
      // 'model_loader','predict_offline','private_publish','json_editor','iv', 'kmeans', 'neural_network','unknown']
      if(d==='task_chain'){return "任务链参数配置"}
      if(d==='linear_regression'){return "线性回归参数配置"}
      if(d==='logistic_regression'){return "逻辑回归参数配置"}
      if(d==='poisson_regression'){return "泊松回归参数配置"}
      let ans = ["从各种数据源读取数据", "对数据进行保存","以隐私保护的方式寻找多方私有数据集的交集",
        "将特征值划分到不同的桶中","填充缺失的特征值","归一化处理后将特征值缩放到范围[0,1]","将分类特征列转换为二元向量列",
        "","差分隐私梯度提升决策树","差分隐私随机森林","评估联邦学习模型","从本地文件加载模型/从内存卸载模型","通过指定模型进行离线预测",
        "处理原始私有数据用于隐私发布","Json模式","计算目标列的权重和信息值","k均值聚类","深度神经网络",""]
      return ans[this.indexModules[d]]
    },
    //监听参与方个数
    changePartyNumber(){
      if(/^[1-9]\d*$/.test(this.partyNumber)) {
        this.taskChainForm = {
          task_type: 'task_chain', party_names: [], messenger_server: [],
          save_model: true, messenger_type: []
        };
        this.$global.partyNumber = parseInt(this.partyNumber);
        this.partyConfig = [];
        this.task_chain_party_names = [];
        let count = 1
        for (let i = 0; i < this.$global.partyNumber; i++) {
          let p = {'name': count};
          this.partyConfig.push(p);
          this.task_chain_party_names.push(null);
          count++;
        }
        this.handleMessengerServer();
        this.handleMessengerType();
        this.showPartyNames = true;
        this.saveTaskChain();
      }
    },
    //检验partyNumber是否为正整数
    checkPartyNumber(){
      if(!/^[1-9]\d*$/.test(this.partyNumber)){
        this.$notify.error({
          title: '提示',
          message: '参与方个数需要为正整数'
        });
        return false;
      }
      return true;
    },
    //处理参与方名称
    handlePartyNames(){
      this.taskChainForm.party_names = this.task_chain_party_names;
      let save = false;
      for(let i=0;i<this.task_chain_party_names.length;i++){
        if(this.task_chain_party_names[i] !== null && this.task_chain_party_names[i]!==''){
          save = true;
        }
      }
      if(save){
        this.saveTaskChain();
      }
    },
    //处理消息服务器地址
    handleMessengerServer(){
      this.taskChainForm.messenger_server = []
      if(this.task_chain_messenger_server===null || this.task_chain_messenger_server===''){
        for(let i=0;i<this.$global.partyNumber;i++){
          this.taskChainForm.messenger_server.push(null)
        }
        return;
      }
      if(this.$global.partyNumber>0 && this.task_chain_messenger_server!==null){
        let ms = this.task_chain_messenger_server.split(',');
        for(let i=0;i<this.$global.partyNumber;i++){
          this.taskChainForm.messenger_server.push(ms)
        }
      }
      if(this.task_chain_messenger_server!==null && this.task_chain_messenger_server!==''){
        this.saveTaskChain();
      }
    },
    //处理消息类型
    handleMessengerType(){
      this.taskChainForm.messenger_type = [];
      for(let i=0;i<this.$global.partyNumber;i++){
        this.taskChainForm.messenger_type.push(this.task_chain_messenger_type);
      }
      if(this.task_chain_messenger_type!==null && this.task_chain_messenger_type!==''){
        this.saveTaskChain();
      }
    },
    //把task_chain的参数存储到moduleTable，并放在localstorage里
    saveTaskChain(){
      this.$global.taskChainForm = this.taskChainForm;
      //如果moduleTable里存在模块且第一个模块不是task_chain，则在开头添加taskChain
      if(this.moduleTable.length>1 && this.moduleTable[0].task_type !== 'task_chain'){
        this.moduleTable.unshift(this.taskChainForm);
      }
      Vue.set(this.moduleTable,0,this.taskChainForm);
      this.$global.writeLocalStorage('jeddak_task_chain',this.taskChainForm);
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
    },
    //在加载时反向解析出来task_chain
    parseTaskChain(form){
      this.showPartyNames = true;
      this.partyNumber = form.party_names.length;
      this.changePartyNumber();
      for(let i=0; i<this.partyNumber;i++){
        this.task_chain_party_names[i] = form.party_names[i];
        this.taskChainForm.party_names[i] = form.party_names[i];
        this.taskChainForm.messenger_server[i] = form.messenger_server[i];
        this.taskChainForm.messenger_type[i] = form.messenger_type[i];
      }
      if(form.messenger_type[0]!==null){
        this.task_chain_messenger_type = form.messenger_type[0];
        this.taskChainForm.save_model = form.save_model;
      }
      this.taskChainForm.save_model = form.save_model;
      this.task_chain_messenger_server = '';
      if(form.messenger_server[0]!==null){
        if(typeof(form.messenger_server[0])=='string'){
          this.task_chain_messenger_server = form.messenger_server[0];
        }else{
          for(let i=0;i<form.messenger_server[0].length;i++){
            this.task_chain_messenger_server = this.task_chain_messenger_server + ',' + form.messenger_server[0][i];
          }
          this.task_chain_messenger_server = this.task_chain_messenger_server.substring(1);
        }
      }
      Vue.set(this.moduleTable,0,this.taskChainForm);
    },
    //初始化TaskChain
    initTaskChain(){
      this.taskChainForm = {task_type: 'task_chain', party_names: [], messenger_server: [],
        save_model: true, messenger_type: []};
      this.partyConfig=[];
      this.partyNumber= null;
      this.showPartyNames= true;
      this.task_chain_messenger_server= null;
      this.task_chain_party_names=[];
      this.task_chain_messenger_type='';
    },
    // choose module and add it in dialog
    addModule() {
      if(!/^[1-9]\d*$/.test(this.partyNumber)){
        this.$notify.error({
          title: '提示',
          message: '在添加模块前请先配置参与方个数和任务链参数'
        });
        return;
      }
      if(this.currentModule === ""){
        this.indexOfCurrentAddModule = 19
      }else {
        this.indexOfCurrentAddModule = this.indexModules[this.currentModule];
      }
      this.$global.isEdit = false;
      this.moduleDialogVisible = true;
    },
    //设置添加或修改对话框是否可见
    setVisible(res){
      this.moduleDialogVisible = res;
      this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
      this.moduleTable[0] = this.taskChainForm;
      //下面的两行代码是为了强制刷新table，因为需要Vue.set才能更新table
      let temp = this.moduleTable[0];
      Vue.set(this.moduleTable,0,temp);
    },
    //设置添加或修改对话框的宽度
    setDialogWidth(w){
      this.dialogWidth = w;
    },
    // edit module
    editModule(sc) {
      this.$global.indexOfModuleTable = sc.$index;
      this.currentModule = sc.row.task_type;
      if(this.currentModule === 'task_chain'){
        this.$notify.info({
          title: '提示',
          message: '请直接在本页面配置任务链参数'
        });
        return;
      }
      if(this.currentModule === 'linear_regression' || this.currentModule === 'logistic_regression'
          || this.currentModule === 'poisson_regression'){
        this.currentModule = 'glm';
      }
      this.indexOfCurrentAddModule = this.indexModules[this.currentModule];
      this.$global.isEdit = true;
      this.moduleDialogVisible = true;
    },
    jsonModule(sc){
      this.$global.indexOfModuleTable = sc.$index;
      this.$global.isEdit = true;
      this.editJsonModuleDialogVisible=true;
    },
    deleteModule(sc) {
      // 1. delete the item in moduleTable
      //如果删除task chain，则初始化task chain
      if(sc.$index === 0){
        this.initTaskChain();
      }
      // arr.slice(x,y) is to delete the elements whose starting index is from x to x+y-1 in array "arr"
      this.moduleTable.splice(sc.$index,1);
      // console.log(this.moduleTable)
      // 2. store in localStrage
      this.$global.writeLocalStorage('jeddak_module_table', this.moduleTable);
    },
    // close dialog
    moduleDialogClose() {
      this.moduleDialogVisible = false;
    },
  }
}
</script>

