<template>
  <div class="predict_offline">
    <el-form ref="data" :model="predictOfflineForm" label-position="left" label-width="160px">
      <!-- task role -->
      <el-form-item v-for="(item,index) in roleConfig" :key="item.name" :label="roleLabel[index]">
        <el-radio-group v-model="roles[index]">
          <el-radio label="guest">guest</el-radio>
          <el-radio label="host">host</el-radio>
        </el-radio-group>
      </el-form-item>
      <!--  model_id -->
      <el-form-item label="模型id:">
        <el-input v-model="model_id" placeholder="请输入模型id"></el-input>
      </el-form-item>
      <!-- input data path -->
      <el-form-item label="要加载的数据路径:">
        <el-input v-for="(item,index) in input_data_path_config" :key="item.name"
                  v-model="input_data_path[index]" :placeholder="input_data_path_PlaceHolder[index]">
        </el-input>
      </el-form-item>
      <el-form-item>
        <div style="float: right;">
          <el-button ref="addOrEditButton" type="primary" @click="addButton">添加</el-button>
          <el-button type="info" @click="closeDialog">取消</el-button>
          <el-button @click="clearForm">清空</el-button>
        </div>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "PredictOffline",
  props: ['data'],
  mounted() {
    //加载时动态设置对话框的宽度
    this.$emit('setDialogWidth','35%');
    //初始化参数
    this.initPara();
    this.moduleTable = this.$global.readLocalStorage('jeddak_module_table');
    //预先加载上次正确的配置，或加载需要修改的配置
    let temp = null;
    if(this.$global.isEdit){
      temp = this.moduleTable[this.$global.indexOfModuleTable];
      //把添加按钮更换为修改
      this.$refs.addOrEditButton.$el.innerText = '修改';
    }else{
      temp = this.$global.readLocalStorage('jeddak_predictoffline');
    }
    //如果参与方个数不一致，则需要重新配置参数
    if(temp !== null){
      if(temp.task_role !==null && temp.task_role.length===this.$global.partyNumber){
        this.parsePara(temp);
      }else{
        this.$notify.info({
          title: '提示',
          message: '检测到本次修改或上次保存的模块配置参与方与任务链的参与方个数不一致，请检查或对此模块重新配置'
        });
      }
    }
  },
  data() {
    return {
      moduleTableL:[],
      predictOfflineForm:{task_type:'predict_offline',task_role:[],model_id:[],input_data_path:[]},
      //角色
      roles:[],
      roleConfig:[],
      roleLabel:[],
      //其他参数
      model_id:null,
      input_data_path:[],
      input_data_path_config:[],
      input_data_path_PlaceHolder:[]
    };
  },
  methods: {
    //关闭对话框
    closeDialog(){
      this.$emit('setVisible',false);
    },
    //初始动态化化参数
    initPara(){
      // 动态初始化角色
      let role = this.$global.formatPartyPara(this.roles, this.roleConfig, this.roleLabel,
          this.$global.partyNumber, '任务角色:','role');
      this.roles = role[0];
      this.roleConfig = role[1];
      this.roleLabel = role[2];
      // 动态初始化input_data_path
      let path = this.$global.formatPartyPara(this.input_data_path, this.input_data_path_config,
          this.input_data_path_PlaceHolder, this.$global.partyNumber, '的数据路径', 'path')
      this.input_data_path = path[0];
      this.input_data_path_config = path[1];
      this.input_data_path_PlaceHolder = path[2];
    },
    //点击添加按钮的事件
    addButton(){
      this.summarizePara();
      this.$global.writeLocalStorage('jeddak_predictoffline',this.predictOfflineForm);
      if(this.$global.isEdit){
        this.moduleTable[this.$global.indexOfModuleTable] = this.predictOfflineForm;
      }else{
        this.moduleTable.push(this.predictOfflineForm);
      }
      this.$global.writeLocalStorage('jeddak_module_table',this.moduleTable);
      this.closeDialog();
    },
    //汇总参数为表单
    summarizePara(){
      this.predictOfflineForm = {task_type:'predict_offline',task_role:[],model_id:[],input_data_path:[]};
      this.predictOfflineForm.task_role = this.roles;
      this.predictOfflineForm.input_data_path = this.input_data_path;
      for(let i=0;i<this.$global.partyNumber;i++){
        this.predictOfflineForm.model_id.push(this.model_id);
      }
    },
    //从表单解析出来各个参数
    parsePara(form){
      this.roles = form.task_role;
      this.model_id = form.model_id[0];
      this.input_data_path = form.input_data_path;
    },
    //清空表单
    clearForm(){
      this.predictOfflineForm = {task_type:'predict_offline',task_role:[],model_id:[],input_data_path:[]};
      this.initPara();
      this.model_id = null;
    },
  }
}
</script>

